import { Component, OnInit, Input, ViewEncapsulation, OnDestroy } from "@angular/core";
import { MatTableDataSource } from "@angular/material";
import { SelectedDevice } from "app/interfaces/selected-device";
import { SelectionModel } from "@angular/cdk/collections";
import { Router } from "@angular/router";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { SupportedFeatures } from 'app/enums/supported-features.enum';

@Component({
	selector: "coordinator-passive-list",
	templateUrl: "./coordinator-passive-list.component.html",
	styleUrls: ["./coordinator-passive-list.component.scss"],
	encapsulation: ViewEncapsulation.None
})
export class CoordinatorPassiveListComponent implements OnInit, OnDestroy {
	@Input() showHeader: Boolean = true;

	private _deviceList: SelectedDevice[] = [];

	@Input()
	set deviceList(deviceList: SelectedDevice[]) {
		this._deviceList = deviceList;

		this.dataSource = new MatTableDataSource<SelectedDevice>(
			this._deviceList
		);
		this.selection = new SelectionModel<SelectedDevice>(
			true,
			this._deviceList
		);
	}

	@Input() selectedFeature: string;

	dataSource = new MatTableDataSource<SelectedDevice>(this._deviceList);
	selection = new SelectionModel<SelectedDevice>(true, this._deviceList);

	constructor(
		private router: Router,
		private rtlsUtilService: RtlsUtilService
	) { }

	ngOnInit() { }

	ngOnDestroy() { }

	/** Whether the number of selected elements matches the total number of rows. */
	isAllSelected() {
		const numSelected = this.selection.selected.length;
		const numRows = this.dataSource.data.length;
		return numSelected === numRows;
	}

	/** Selects all rows if they are not all selected; otherwise clear selection. */
	toggle() {
		this.isAllSelected()
			? this.selection.clear()
			: this.dataSource.data.forEach(row => this.selection.select(row));

		this.update_device_selection();
	}

	/** The label for the checkbox on the passed row */
	checkboxLabel(row?: SelectedDevice): string {
		if (!row) {
			return `${this.isAllSelected() ? "select" : "deselect"} all`;
		}
		return `${this.selection.isSelected(row) ? "deselect" : "select"} row`;
	}

	async goToDeviceSelection() {
		localStorage.setItem("activeDeviceList", JSON.stringify(this._deviceList));

		await this.rtlsUtilService.disable_all_aoa();

		await this.rtlsUtilService.disable_all_cci();

		await this.rtlsUtilService.diconnect_all_responders();

		this.router.navigate(["/devices-selection-page", { feature: this.selectedFeature }]);
	}

	selection_toggle(row: SelectedDevice) {
		this.selection.toggle(row);

		this.update_device_selection();
	}

	update_device_selection() {
		this._deviceList.forEach((device, index) => {
			device.selected = false;
		});

		this.selection.selected.forEach(selected_device => {
			let index = this._deviceList.indexOf(selected_device);
			this._deviceList[index].selected = true;
		});

		this.rtlsUtilService.update_device_list(this._deviceList);
	}

	getDisplayedColumns(): string[] {
		let displayedColumnsAOA = [
			"select",
			"bd_address",
			"rtls_role",
			"aoa_role",
			"graph_color"
		];

		return displayedColumnsAOA;
	}
}
