import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Device } from 'app/interfaces/device';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { RestApiService } from 'app/services/rest-api.service';
import { SelectedDevice } from 'app/interfaces/selected-device';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { ErrorDialogComponent } from 'app/dialogs/error-dialog/error-dialog.component';
import { MatDialog } from '@angular/material';
import { log } from 'util';
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { environment } from 'environments/environment';

@Component({
	selector: 'devices-selection',
	templateUrl: './devices-selection.component.html',
	styleUrls: ['./devices-selection.component.scss']
})
export class DevicesSelectionComponent implements OnInit, OnDestroy {

	spinner_message: string = "Getting Device List ...";

	deviceList: Device[] = [];

	rtlsUtilServiceErrorOccurSubscribeObj: any;
	rtlsUtilServiceMsgForSpinnerSubscribe: any;

	selectedFeature: SupportedFeatures;

	title = "Select Devices";
	warnning_message: string = "";

	constructor(
		private route: ActivatedRoute,
		private spinner: NgxSpinnerService,
		private router: Router,
		private toastr: ToastrService,
		public dialog: MatDialog,
		private rtlsUtilService: RtlsUtilService
	) { }

	ngOnInit() {
		this.route.paramMap.subscribe(params => {
			this.selectedFeature = <SupportedFeatures>(params.get('feature'));

			if (this.selectedFeature == SupportedFeatures.CL_AOA) {
				this.title = "Select Coordinator";
			}
		});

		this.rtlsUtilServiceErrorOccurSubscribeObj = this.rtlsUtilService.errorOccur.subscribe(errorType => {
			this.spinner.hide();

			const dialogRef = this.dialog.open(ErrorDialogComponent, {
				width: '500px',
				height: 'auto',
				disableClose: true,
				data: { 'error_type': errorType }
			});
		})

		this.rtlsUtilServiceMsgForSpinnerSubscribe = this.rtlsUtilService.msgForSpinner.subscribe(msg => {
			if (msg) {
				this.spinner_message = msg;
				this.spinner.show();
			} else {
				this.spinner.hide();
			}
		});

		this.refresh_device_list();
	}

	ngOnDestroy() {
		let selected_devices = this.deviceList.filter(d => d.selected);

		localStorage.setItem('selectedDevicesList', JSON.stringify(selected_devices));
		localStorage.setItem('activeDeviceList', "");

		this.rtlsUtilServiceErrorOccurSubscribeObj.unsubscribe();
		this.rtlsUtilServiceMsgForSpinnerSubscribe.unsubscribe();
	}

	select_all() {
		this.deviceList.forEach(device => {
			device.selected = true;
		});

		this.check_if_coonnection_monitor_device_selected();
	}

	select_none() {
		this.deviceList.forEach(device => {
			device.selected = false;
		});

		this.check_if_coonnection_monitor_device_selected();
	}

	refresh_device_list() {
		this.rtlsUtilService.get_device_list(this.selectedFeature).then(
			deviceList => {
				this.deviceList = deviceList;

				let activeDeviceList = localStorage.getItem('activeDeviceList');
				if (activeDeviceList && activeDeviceList !== "") {

					let activeDeviceListParsed: SelectedDevice[] = JSON.parse(activeDeviceList);
					for (let d of activeDeviceListParsed) {
						for (let index = 0; index < this.deviceList.length; index++) {
							var device = this.deviceList[index];

							var matchParam = device.parameters.find(p => p.label === "Port" && p.value === d.port);
							if (matchParam !== undefined) {
								device.selected = true;

							}
						}
					}
				}
			},
			error => {
				console.log(error);
			}
		)
	}

	indentify_devices() {
		this.rtlsUtilService.indentify_devices(this.deviceList, this.selectedFeature).then(deviceList => this.deviceList = deviceList);
	}

	is_something_selected(): Boolean {
		return this.deviceList.filter(d => d.selected).length > 0;
	}

	number_of_coordinators_in_list(devices): Number {
		let count = 0;

		for (let index = 0; index < devices.length; index++) {
			const element = devices[index];

			for (let i = 0; i < element.parameters.length; i++) {
				const parameter = element.parameters[i];
				if (parameter.label === "RTLS" && parameter.value === "COORDINATOR") {
					count++;
				}
			}
		}

		return count;
	}

	number_of_hadm_devices_in_list(devices): Number {
		let count = 0;

		for (let index = 0; index < devices.length; index++) {
			const element = devices[index];

			for (let i = 0; i < element.parameters.length; i++) {
				const parameter = element.parameters[i];
				if (parameter.label === "HADM Support" && parameter.value === "Yes") {
					count++;
				}
			}
		}

		return count;
	}

	number_of_cl_aoa_devices_in_list(devices): Number {
		let count = 0;

		for (let index = 0; index < devices.length; index++) {
			const element = devices[index];

			for (let i = 0; i < element.parameters.length; i++) {
				const parameter = element.parameters[i];
				if (parameter.label === "CL AOA Support" && parameter.value === "Yes") {
					count++;
				}
			}
		}

		return count;
	}

	is_selected_devices_valid(devices): boolean {
		let count = 0;

		for (let index = 0; index < devices.length; index++) {
			const element = devices[index];

			for (let i = 0; i < element.parameters.length; i++) {
				const parameter = element.parameters[i];
				if (parameter.label === "Device" && parameter.value === "N/A") {
					return false;
				}
			}
		}

		return true;
	}


	check_if_coonnection_monitor_device_selected() {
		this.warnning_message = "";

		for (let index = 0; index < this.deviceList.length; index++) {
			const element = this.deviceList[index];

			if (element.selected && element.parameters[3].value === "CONNECTION MONITOR" && !environment.aoa_raw_only) {
				this.warnning_message = 'You selected "CONNECTION MONITOR" device it will disable AoA featutes in the dashboard view';
				break;
			}
		}
	}


	continue() {
		let selected_devices = this.deviceList.filter(d => d.selected);

		let num_of_coordinators = this.number_of_coordinators_in_list(selected_devices);
		let num_of_cl_aoa_devices = this.number_of_cl_aoa_devices_in_list(selected_devices);

		if (!this.is_selected_devices_valid(selected_devices)) {
			this.toastr.error('One of selected device fail to indentified as valid device', 'Selected Device Error', {
				timeOut: 3000,
				positionClass: "toast-bottom-full-width"
			});
		} else if (num_of_coordinators == 0) {
			this.toastr.error('At least of of the devices must be RTLS COORDINATOR', 'Selected Device Error', {
				timeOut: 3000,
				positionClass: "toast-bottom-full-width"
			});
		} else if (num_of_coordinators > 1) {
			this.toastr.error('Only one RTLS COORDINATOR allowed', 'Selected Device Error', {
				timeOut: 2000,
				positionClass: "toast-bottom-full-width"
			});
		} else if (this.selectedFeature == SupportedFeatures.CL_AOA && num_of_cl_aoa_devices !== selected_devices.length) {
			this.toastr.error('One of selected devices not support CL AOA', 'Selected Device Error', {
				timeOut: 2000,
				positionClass: "toast-bottom-full-width"
			});
		} else {
			this.router.navigate(['/dashboard-page', { 'feature': this.selectedFeature }]);
		}
	}

	deviceSelected(device) {
		this.check_if_coonnection_monitor_device_selected();
	}


}
