import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { SerialPortService } from 'app/services/serial-port.service';
import { NgTerminal, FunctionsUsingCSI, DisplayOption, KindOfEraseInDisplay } from 'ng-terminal';
import { interval } from 'rxjs/observable/interval';
import { takeWhile, filter, scan, map, withLatestFrom, switchMap } from 'rxjs/operators';

@Component({
	selector: 'serial-port-viewer',
	templateUrl: './serial-port-viewer.component.html',
	styleUrls: ['./serial-port-viewer.component.scss'],
	// encapsulation: ViewEncapsulation.None,
})
export class SerialPortViewerComponent implements OnInit {
	@ViewChild('term', { static: true }) terminal: NgTerminal;

	displayOptionBounded:DisplayOption = {
		fixedGrid: {
			rows: 16,
			cols: 60,
		}
	}
	avaliable_baudrates: number[];
	selected_baudrate: number = 115200;
	comport_connected: boolean = false;

	comport_output_obs: any;

	constructor(private serialPortService: SerialPortService) { }

	ngOnInit() {
		this.avaliable_baudrates = this.serialPortService.baudrates;

		this.serialPortService.clear_terminal.subscribe(clean => {
			if (clean && this.terminal) {


				this.terminal.write(FunctionsUsingCSI.eraseInDisplay(KindOfEraseInDisplay.All));

			}
		});

		this.serialPortService.comport_connection_status.subscribe(status =>
			this.comport_connected = status
		);
	}

	connect() {
		this.comport_output_obs = this.serialPortService.comport_output.subscribe(output => {
			if (this.terminal) {
				this.terminal.write(output);
			}
		})

		this.serialPortService.connect(this.selected_baudrate);
	}

	disconnect() {
		this.serialPortService.disconnect();

		if (this.comport_output_obs) {
			this.comport_output_obs.unsubscribe();
		}
	}

}
