import { Injectable } from '@angular/core';
import { Subject, of, throwError, Observable } from 'rxjs';
import { WebsocketService } from './websocket.service';
import { map, catchError } from 'rxjs/operators';

@Injectable({
	providedIn: 'root'
})
export class ContinuousDataService {

	public data: Subject<any>;
	private _connected = new Subject<any>();

	constructor(private wsService: WebsocketService) { }

	connect(port: number) {
		this.data = <Subject<any>>this.wsService.connect(`ws://127.0.0.1:${port}`)
			.pipe(
				map((response: MessageEvent): any => {
					let data = JSON.parse(response.data);
					return data;
				}),
				catchError(err => {
					return throwError(err);
				})
			).share();

		// .map((response: MessageEvent): any => {
		// 	console.log(response);
		// 	let data = JSON.parse(response.data);
		// 	return data;
		// }).catch(err => { return of([]) }).share();

		this.setConnected("connected");
	}

	setConnected(event) {
		this._connected.next(event);
	}

	isConnected(): Subject<any> {
		return this._connected;
	}

	disconnect() {
		this.wsService.close();
	}

}
