import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectedDevice } from 'app/interfaces/selected-device';
import * as pluginDataLabels from "chartjs-plugin-datalabels";
import { BaseChartDirective, Label, Color } from 'ng2-charts';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import { ContinuousDataService } from 'app/services/continuous-data.service';
import { ConfigurationService } from 'app/services/configuration.service';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { MatDialog } from '@angular/material';
import { ResponderListService } from 'app/services/responder-list.service';
import { ConfigurationDialogComponent } from 'app/dialogs/configuration-dialog/configuration-dialog.component';
import { ConfigurationType } from 'app/enums/configuration-type.enum';
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { Responder } from 'app/interfaces/responder';

@Component({
	selector: 'txpower-rssi-chart',
	templateUrl: './txpower-rssi-chart.component.html',
	styleUrls: ['./txpower-rssi-chart.component.scss']
})
export class TxpowerRssiChartComponent implements OnInit {
	_deviceList: SelectedDevice[];

	@ViewChild(BaseChartDirective, { static: false })
	public chart: BaseChartDirective;

	public barChartOptions: ChartOptions = {
		responsive: true,
		maintainAspectRatio: false,

		// We use these empty structures as placeholders for dynamic theming.
		scales: {
			xAxes: [{}],
			yAxes: [{}]
		},
		plugins: {
			datalabels: {
				anchor: "end",
				align: "end"
			}
		},
		tooltips: {
			enabled: false
		}
	};

	public barChartLabels: Label[] = ['RSSI', 'TX Power'];;
	public barChartType: ChartType = "bar";
	public barChartLegend = false;
	public barChartPlugins = [pluginDataLabels];
	public barChartColors: Color[] = [];
	public barChartData: ChartDataSets[];

	private scale_min: number;
	private scale_max: number;
	private scale_step: number;

	private s1: any;
	private s2: any;
	private s3: any;
	private s4: any;
	private s5: any;
	private s6: any;

	private auto_scale_enabled = false;
	public is_any_responder_padv_report_started: boolean = false;

	constructor(
		private continuousData: ContinuousDataService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		public dialog: MatDialog,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		console.info("ngOnInit of TxpowerRssiChartComponent");

		this.s1 = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			this.scale_min = -100;
			this.scale_max = 100;
			this.scale_step = 10;

			this.barChartColors = Array.from(
				new_configuration.general.color_schema.slice(1),
				(c: String) => {
					return {
						backgroundColor: c as Color
					}
				}
			) as Color[];

			this.update_scale();
		});

		this.s2 = this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("TxpowerRssiChartComponent subscribe to flow data");
				this.continuousData.data.subscribe(
					recv_data => {
						if (
							recv_data &&
							recv_data.message.command ===
							"RTLS_EVT_PERIODIC_ADV_RPT"
						) {
							setTimeout(() => {
								this.updateChart2(recv_data);
							}, 15);
						}
					},
					error => {
						console.log(error);
					}
				);
			}
		});

		this.s3 = this.rtlsUtilService.initialize_done.subscribe(() => {
			this.initChart();
		});

		this.s4 = this.responderListService.on_responder_list_change().subscribe(responder_list => {
			this.initChart();
		});

		this.s5 = this.responderListService.on_periodic_adv_responder_list_change().subscribe(periodic_adv_responder_list => {
			this.initChart();
		});

		this.s6 = this.responderListService.isAnyResponderPadvReportStarted.subscribe(status => {
			this.is_any_responder_padv_report_started = status;
		});
	}

	ngOnDestroy() {
		console.info("ngOnDestroy of TxpowerRssiChartComponent");

		this.s1.unsubscribe();
		this.s2.unsubscribe();
		this.s3.unsubscribe();
		this.s4.unsubscribe();
		this.s5.unsubscribe();
		this.s6.unsubscribe();
	}

	initChart() {
		let sync_responders = this.responderListService.get_synced_responders();
		sync_responders.sort((s1, s2) => s1.connection_handle - s2.connection_handle);

		if (sync_responders.length > 0) {
			this.barChartData = Array.from(
				sync_responders,
				(s: Responder) => {
					return {
						data: [0, 0],
						label: s.connection_handle.toString()
					}
				}
			);
		} else {
			this.barChartData = [{
				data: [],
				label: ''
			}];
		}


		if (!this.auto_scale_enabled) {
			this.barChartOptions.scales.yAxes[0].ticks = {
				min: this.scale_min,
				max: this.scale_max,
				stepSize: this.scale_step
			};
		}
	}

	updateChart2(recv_data) {
		let sync_handle = recv_data.message.payload.syncHandle;


		let index = this.barChartData.findIndex(d => {
			return d.label === sync_handle.toString();
		});

		this.barChartData[index].data = [recv_data.message.payload.rssi, recv_data.message.payload.txPower !== 127 ? recv_data.message.payload.txPower : 0];

		this.barChartData = this.barChartData.slice();
		this.rtlsUtilService.logMessages.next(recv_data);
	}

	downloadCanvas(event) {
		var canvas = document.getElementById("per-adv-rpt-canvas") as HTMLCanvasElement;

		var context = canvas.getContext("2d");

		//cache height and width
		var w = canvas.width;
		var h = canvas.height;

		var data = context.getImageData(0, 0, w, h);

		var compositeOperation = context.globalCompositeOperation;

		context.globalCompositeOperation = "destination-over";
		context.fillStyle = "#fff";
		context.fillRect(0, 0, w, h);

		var imageData = canvas.toDataURL("image/jpeg");

		context.clearRect(0, 0, w, h);
		context.putImageData(data, 0, 0);
		context.globalCompositeOperation = compositeOperation;

		var a = document.createElement("a");
		a.href = imageData;
		a.download = "periodic-advertise-report.jpeg";
		a.click();
	}

	showClAoaConfiguration() {
		const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
			width: "900px",
			height: "auto",
			disableClose: true,
			data: {
				configuration: this.configurationService.getCurrentConfiguration(),
				active_feature: SupportedFeatures.CL_AOA,
				selected_tab: 5
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log("The dialog was closed", result);
			if (result !== undefined) {
				this.configurationService.setConfiguration(result, ConfigurationType.CL_AOA);
			}
		});
	}

	auto_scale() {
		this.auto_scale_enabled = !this.auto_scale_enabled;

		this.update_scale();
	}

	update_scale() {
		if (this.chart) {
			if (this.auto_scale_enabled) {
				delete this.chart.chart.options.scales.yAxes[0].ticks;
			} else {
				this.chart.chart.options.scales.yAxes[0].ticks = {
					min: this.scale_min,
					max: this.scale_max,
					stepSize: this.scale_step
				};
			}

			this.chart.chart.update();
			this.chart.chart.render();
		}
	}
}
