import { Component, OnInit, AfterViewChecked, OnDestroy } from '@angular/core';
import { SelectedDevice } from 'app/interfaces/selected-device';
import { Responder } from 'app/interfaces/responder';
import { ContinuousDataService } from 'app/services/continuous-data.service';
import { ConfigurationService } from 'app/services/configuration.service';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { MatDialog } from '@angular/material';
import { ResponderListService } from 'app/services/responder-list.service';
import { Configuration } from 'app/interfaces/configuration.js';

import sigma from 'sigma';
(<any>window).sigma = sigma;
import('../../../../node_modules/sigma/plugins/sigma.plugins.dragNodes/sigma.plugins.dragNodes.js')

@Component({
	selector: 'space-view',
	templateUrl: './space-view.component.html',
	styleUrls: ['./space-view.component.scss']
})
export class SpaceViewComponent implements OnInit, AfterViewChecked, OnDestroy {

	_deviceList: SelectedDevice[];

	sigmaJs: any;
	sigmaJsData: any;

	_current_view_devices: any[];
	_connectedResponder: Responder = null;
	_responder_x: number;
	_responder_y: number;

	private configurationServiceOnConfigurationChangeSubscribeObj: any
	private continuousDataIsConnectedSubscribeObj: any
	private rtlsUtilServiceInitializeDoneSubscribeObj: any
	private rtlsUtilServiceDeviceListSubscribeObj: any
	private rtlsUtilServiceTofEnabledSubscribeObj: any;
	private rtlsUtilServiceAoaEnabledSubscribeObj: any;
	private responderListServiceOnResponderListChangeSubscribeObj: any;

	private points_per_meter: number;
	private aoa_average_each_n_point: number;
	private tof_average_each_n_point: number;

	avaliable_scale = [
		{ 'name': '25p = 1m', 'value': 25 },
		{ 'name': '50p = 1m', 'value': 50 },
		{ 'name': '75p = 1m', 'value': 75 },
		{ 'name': '100p = 1m', 'value': 100 },
		{ 'name': '150p = 1m', 'value': 150 },
		{ 'name': '200p = 1m', 'value': 200 },
		{ 'name': '250p = 1m', 'value': 250 }
	];

	constructor(
		private continuousData: ContinuousDataService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		public dialog: MatDialog,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		console.debug("ngOnInit of space-view component");

		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			// Return this with HADM
			// this.points_per_meter = new_configuration.general.space_view_parameters.points_per_meter;
			// Delete this with HADM
			this.points_per_meter = 70;
			this.aoa_average_each_n_point = new_configuration.general.aoa_graph_parameters.average_each_n_point - 1;

			this.draw_axis();
		});

		this.continuousDataIsConnectedSubscribeObj = this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("Space view subscribe to flow data")
				this.continuousData.data.subscribe(recv_data => {
					if (recv_data) {
						setTimeout(() => {
							this.updateChart2(recv_data);
						}, 15);
					}
				},
					error => {
						console.log(error);
					}
				);
			}
		});

		this.rtlsUtilServiceInitializeDoneSubscribeObj = this.rtlsUtilService.initialize_done.subscribe(() => {
			this.initView();
		});

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(deviceList => {
			// console.log(deviceList);
			this._deviceList = deviceList;

			this.initView();
		});

		this.rtlsUtilServiceTofEnabledSubscribeObj = this.rtlsUtilService.hadmEnabled.subscribe((tofEnabled) => {
			// if (!tofEnabled && this._current_view_devices && this._current_view_devices.length > 0) {
			// 	setTimeout(() => {
			// 		this._current_view_devices.forEach(element => {
			// 			element.distance = 1;
			// 		});
			// 	}, 500);
			// }
		});

		this.rtlsUtilServiceAoaEnabledSubscribeObj = this.rtlsUtilService.aoaEnabled.subscribe((aoaEnabled) => {
			// if (!aoaEnabled && this._current_view_devices && this._current_view_devices.length > 0) {
			// 	setTimeout(() => {
			// 		this._current_view_devices.forEach(element => {
			// 			element.angle = 0;
			// 		});
			// 	}, 500);
			// }
		});

		this.responderListServiceOnResponderListChangeSubscribeObj = this.responderListService.on_responder_list_change().subscribe(responderList => {
			let connected_responders = responderList.filter(s => s.is_connected);
			if (connected_responders.length > 0) {
				let responder_in_view = connected_responders.find(s => s.in_view);

				if (this._connectedResponder !== responder_in_view) {
					this.add_remove_responder(null);
					setTimeout(() => {
						this.add_remove_responder(responder_in_view);
					}, 100);
				}
			} else {
				this.add_remove_responder(null);
			}
		});

		this.sigmaJs = new sigma(
			{
				renderer: {
					container: document.getElementById('sigmajs-container'),
					type: 'canvas'
				},
				settings: {
					labelThreshold: 1,
					// autoRescale: ['nodeSize', 'edgeSize'],
					autoRescale: false,
					minNodeSize: 0,
					maxNodeSize: 0,
					minEdgeSize: 0,
					maxEdgeSize: 0,
					nodesPowRatio: 1,
					edgesPowRatio: 1,
					enableCamera: false
				}
			}
		);

		// this.sigmaJs.cameras[0].bind('coordinatesUpdated', (e) => {
		// 	console.debug(this.sigmaJs.cameras[0]);
		// 	this.sigmaJs.cameras[0].x = 0;
		// 	this.sigmaJs.cameras[0].y = 0;
		// 	this.sigmaJs.cameras[0].ratio = 1;
		// })
	}

	sigma_plugin_init: boolean = false;

	ngAfterViewChecked() {
		if (this.sigmaJs && sigma.plugins && !this.sigma_plugin_init) {
			var dragListener = new sigma.plugins.dragNodes(this.sigmaJs, this.sigmaJs.renderers[0]);

			dragListener.bind('drag', (event) => {
				let node = event.data.node;
				// console.log(node);
				node.label = `${node.rtls_role.slice(0, 1)}_${node.id.slice(9)} (${Math.floor(node.x)}, ${Math.floor(node.y)})`;
			})

			this.sigma_plugin_init = true;
		}
	}

	ngOnDestroy() {
		console.debug("ngOnDestroy of space-view component")

		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.continuousDataIsConnectedSubscribeObj.unsubscribe();
		this.rtlsUtilServiceInitializeDoneSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
		this.rtlsUtilServiceTofEnabledSubscribeObj.unsubscribe();
		this.rtlsUtilServiceAoaEnabledSubscribeObj.unsubscribe();
		this.responderListServiceOnResponderListChangeSubscribeObj.unsubscribe();

		sigma.plugins.killDragNodes(this.sigmaJs);
	}

	refresh_sigmajs() {
		// console.log(this.sigmaJs);
		// console.log(this.sigmaJs.graph.nodes());
		// console.log(this.sigmaJs.graph.edges());
		// console.log(this.sigmaJs.iterNodes);

		// this.sigmaJs.graph.clear();
		// this.sigmaJs.graph.read(this.sigmaJsData);
		if (this.sigmaJs) {
			this.sigmaJs.refresh();
		}
	}

	getRandomInt(max: number) {
		return Math.floor(Math.random() * Math.floor(max));
	}

	initView() {
		if (!this.sigmaJs) { return }

		let selectedDeviceList = this._deviceList.filter(d => d.selected);

		if (selectedDeviceList.length > 0) {

			this.sigmaJs.graph.clear();
			this._current_view_devices = [];

			selectedDeviceList.forEach((d: SelectedDevice) => {
				let x = (d.rtls_role === "MASTER" || selectedDeviceList.length === 1) ? 0 : this.getRandomInt(100) + 10;
				let y = (d.rtls_role === "MASTER" || selectedDeviceList.length === 1) ? 0 : this.getRandomInt(100) + 10;

				this.sigmaJs.graph.addNode({
					"id": d.bd_address,
					"label": `${d.rtls_role.slice(0, 1)}_${d.bd_address.slice(9)} (${x}, ${y})`,
					"x": x,
					"y": y,
					"size": 7,
					"color": d.graph_color,
					"rtls_role": d.rtls_role
				});

				this._current_view_devices.push({
					'id': d.bd_address,
					'angle': 0,
					'distance': 1,
					'aoa_view_counter': 0,
					'aoa_view_sample_sum': 0,
					'tof_view_counter': 0,
					'tof_view_sample_sum': 0
				})
			});

			// this.add_remove_responder(this.rtlsUtilService.getCurrentConnectionStatus());

			if (this._connectedResponder !== null) {
				this.add_responder_into_sigmajs();

				this.create_edges();
			}

		} else {
			this.sigmaJs.graph.clear();
			this._current_view_devices = [];
		}

		this.refresh_sigmajs();

		this.reset_zoom();

		this.draw_axis();
	}

	updateChart2(recv_data) {
		if (!this._connectedResponder) { return }

		if ("connHandle" in recv_data.message.payload && recv_data.message.payload.connHandle !== this._connectedResponder.connection_handle) { return }

		let device = this._current_view_devices.find(d => d.id === recv_data.identifier);

		if (device) {
			if (recv_data.message.command === "RTLS_CMD_AOA_RESULT_ANGLE") {
				let angle = recv_data.message.payload.angle;

				if (device.aoa_view_counter === this.aoa_average_each_n_point) {
					let avg_angle = device.aoa_view_sample_sum / device.aoa_view_counter;
					avg_angle = this.aoa_average_each_n_point === 0 ? angle : avg_angle;
					device.angle = avg_angle;

					device.aoa_view_counter = 0;
					device.aoa_view_sample_sum = 0;

					recv_data.message.payload.angle = avg_angle;
					this.rtlsUtilService.logMessages.next(recv_data);
				} else {
					device.aoa_view_counter += 1;
					device.aoa_view_sample_sum += angle;
				}
			}

			if (recv_data.message.command === "RTLS_CMD_HADM_RESULT_DIST") {
				let distance = recv_data.message.payload.distance;

				if (device.tof_view_counter === this.tof_average_each_n_point) {
					let avg_distance = device.tof_view_sample_sum / device.tof_view_counter;
					avg_distance = this.tof_average_each_n_point === 0 ? distance : avg_distance;
					device.distance = avg_distance;

					device.tof_view_counter = 0;
					device.tof_view_sample_sum = 0;

					recv_data.message.payload.distance = avg_distance;
					this.rtlsUtilService.logMessages.next(recv_data);
				} else {
					device.tof_view_counter += 1;
					device.tof_view_sample_sum += distance;
				}
			}

			this.update_responder_location()
		}
	}

	update_responder_location() {
		let total_x = 0;
		let total_y = 0;

		this._current_view_devices.forEach(node => {
			let sigma_node = this.sigmaJs.graph.nodes().find(n => n.id === node.id);

			let r = 'distance' in node ? node.distance * this.points_per_meter : this.points_per_meter;
			let ang = 'angle' in node ? node.angle : 0;

			let ang_rad = ang * 0.01745329252;

			let x_r_0 = Math.floor(r * Math.cos(ang_rad));
			let y_r_0 = Math.floor(r * Math.sin(ang_rad));

			total_x += sigma_node.x + x_r_0;
			total_y += sigma_node.y + y_r_0;
		})

		this.sigmaJs.graph.nodes().forEach(n => {
			if (n.id === this._connectedResponder.bd_address) {
				n.x = Math.floor(total_x / this._current_view_devices.length);
				n.y = Math.floor(total_y / this._current_view_devices.length);

				n.label = `${n.rtls_role.slice(0, 1)}_${n.id.slice(9)} (${Math.floor(n.x)}, ${Math.floor(n.y)})`;
			}
		})

		this.refresh_sigmajs();
	}

	add_responder_into_sigmajs() {
		let x = this.getRandomInt(100) + 10;
		let y = this.getRandomInt(100) + 10;

		this.sigmaJs.graph.addNode({
			"id": this._connectedResponder.bd_address,
			"label": `${this._connectedResponder.rtls_role.slice(0, 1)}_${this._connectedResponder.bd_address.slice(9)} (${x}, ${y})`,
			"x": x,
			"y": y,
			"size": 7,
			"color": '#cc0000',
			"rtls_role": this._connectedResponder.rtls_role
		});
	}

	add_remove_responder(connected_responder) {
		// console.debug("add_remove_responder", connected_responder);
		if (connected_responder) {
			this._connectedResponder = connected_responder;

			this.add_responder_into_sigmajs();

			this.create_edges();

		} else {
			if (this._connectedResponder) {
				this.sigmaJs.graph.edges().forEach(e => this.sigmaJs.graph.dropEdge(e.id));
				this.sigmaJs.graph.dropNode(this._connectedResponder.bd_address);
			}
			this._connectedResponder = null;
		}

		this.refresh_sigmajs();
	}

	create_edges() {
		this.sigmaJs.graph.nodes().forEach((n, i) => {
			this.sigmaJs.graph.addEdge({
				"id": `e${i}`,
				"source": this._connectedResponder.bd_address,
				"target": n.id
			});
		})
	}

	reset_zoom() {
		this.sigmaJs.cameras[0].goTo({ x: 0, y: 0, angle: 0, ratio: 1 });
	}

	draw_axis() {
		if (!this.sigmaJs) {
			return
		}

		var canvas = document.getElementById("sigmajs-axis") as HTMLCanvasElement;
		canvas.width = this.sigmaJs.renderers[0].width;
		canvas.height = this.sigmaJs.renderers[0].height;

		var ctx = canvas.getContext("2d");

		var canvas_width = canvas.width;
		var canvas_height = canvas.height;

		var center_x = canvas_width / 2;
		var center_y = canvas_height / 2;

		// ctx.scale(0.5, 0.33)
		ctx.lineWidth = 1;
		ctx.strokeStyle = "#A7A7A7";

		var min_width = Math.min(canvas_width, canvas_height)
		var hor_line_start = (canvas_width - min_width) / 2;
		var hor_line_stop = ((canvas_width - min_width) / 2) + min_width;
		var ver_line_start = (canvas_height - min_width) / 2;
		var ver_line_stop = ((canvas_height - min_width) / 2) + min_width;

		ctx.beginPath();
		ctx.moveTo(hor_line_start, center_y);
		ctx.lineTo(hor_line_stop, center_y);
		ctx.stroke();

		ctx.beginPath();
		ctx.moveTo(center_x, ver_line_start);
		ctx.lineTo(center_x, ver_line_stop);
		ctx.stroke();

		ctx.font = "12px Arial";

		let step = this.points_per_meter / 4;
		let i = 1;
		let radius = step * i;
		// let factor = 1.38;

		while (radius < Math.min(center_x, center_y)) {
			ctx.beginPath();
			ctx.arc(center_x, center_y, radius, 0, 2 * Math.PI);
			ctx.stroke();

			// Return with new HADM
			// let t = `${radius / this.points_per_meter} m`
			// ctx.fillText(t, center_x - Math.floor(radius / factor), center_y - Math.floor(radius / factor));

			i += 1;
			radius = step * i;
		}

		let txt = "± 180°";
		ctx.fillText(txt, hor_line_start - ctx.measureText(txt).width, center_y - 5);
		txt = "0°";
		ctx.fillText(txt, hor_line_stop + ctx.measureText(txt).width, center_y - 5);
		txt = "- 90°";
		ctx.fillText(txt, center_x + 5, ver_line_start + ctx.measureText(txt).width - 10);
		txt = "90°";
		ctx.fillText(txt, center_x + 5, ver_line_stop - ctx.measureText(txt).width + 10);
	}

	onScaleChange(e) {
		let curConfiguration: Configuration = this.configurationService.getCurrentConfiguration();
		curConfiguration.general.space_view_parameters.points_per_meter = parseInt(e);
		this.configurationService.setConfiguration(curConfiguration);
	}
}
