import { Injectable } from "@angular/core";
import { Subject, BehaviorSubject } from "rxjs";
import { Device } from "app/interfaces/device";
import { RestApiService } from "./rest-api.service";
import { SelectedDevice } from "app/interfaces/selected-device";
import { ContinuousDataService } from "./continuous-data.service";
import { Responder } from "app/interfaces/responder";
import { SetupCapability } from "app/interfaces/setup-capability";
import { AoaParameters, ClAoaScannerParameters, ClAoaConfigParameters, ClAoaParameters } from "app/interfaces/configuration";
import { ConnectionStatus } from "app/interfaces/connection-status";
import { ErrorType } from "app/enums/error-type.enum";
import { ResponderListService } from "./responder-list.service";
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { SerialPortService } from "./serial-port.service";

@Injectable({
	providedIn: "root"
})
export class RtlsUtilService {
	public errorOccur: Subject<ErrorType> = new Subject<ErrorType>();
	public msgForSpinner: Subject<string> = new Subject<string>();

	public deviceList: Subject<SelectedDevice[]> = new Subject<SelectedDevice[]>();
	public responderList: Subject<Responder[]> = new Subject<Responder[]>();
	public setupCapability: Subject<SetupCapability> = new Subject<SetupCapability>();
	public connectionStatus: BehaviorSubject<ConnectionStatus> = new BehaviorSubject<ConnectionStatus>({ responder: null, success: false, connection_handle: -1 });
	public cciEnabled: Subject<boolean> = new Subject<boolean>();
	public hadmEnabled: Subject<boolean> = new Subject<boolean>();
	public aoaEnabled: Subject<boolean> = new Subject<boolean>();
	public initialize_done: Subject<boolean> = new Subject<boolean>();
	public inSyncProcess: Subject<boolean> = new Subject<boolean>();

	public logMessages: Subject<string> = new Subject<string>();
	public internalLogMessages: Subject<string> = new Subject<string>();

	private autoPlay: boolean = false;

	constructor(
		private restAPI: RestApiService,
		private continuousData: ContinuousDataService,
		private responderListService: ResponderListService,
		private serialPortService: SerialPortService
	) {
		this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("Log box subscribe to flow data");
				this.continuousData.data.subscribe(
					recv_data => {
						let result_msg_ids = [
							"RTLS_EVT_CONN_INFO",
							"RTLS_CMD_HADM_RESULT_DIST",
							"RTLS_CMD_AOA_RESULT_ANGLE",
							"RTLS_EVT_PERIODIC_ADV_RPT",
							"RTLS_CMD_CL_AOA_RESULT_ANGLE"
						];

						if (
							!result_msg_ids.some(
								id => id === recv_data.message.command
							)
						) {
							this.logMessages.next(recv_data);
						}
					},
					error => {
						// console.log(error);
						this.errorOccur.next(ErrorType.WebSocketError);
					}
				);
			}
		});
	}

	handle_rest_api_error(msg) {
		if (msg.status >= 500) {
			this.errorOccur.next(ErrorType.ServerActionError);
		}

		if (msg.status === 0) {
			this.errorOccur.next(ErrorType.ServerCommunicationError);
		}
	}

	initialize(device_list: Device[], websocket_port: number, rtlsutil_timeout: number, aoa_raw_only: boolean) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Initializing ...");
			this.restAPI.initializeRtlsUtil(device_list, websocket_port, rtlsutil_timeout, aoa_raw_only).subscribe(
				deviceList => {
					this.deviceList.next(deviceList);
					this.continuousData.connect(websocket_port);

					this.responderListService.clear_all_responder_in_view();
					this.responderListService.clear_all_responder_connection_status();
					this.responderListService.clear_all_responder_cci_started_status();
					this.responderListService.clear_all_responder_aoa_started_status();
					this.responderListService.clear_all_responder_hadm_started_status();
					this.responderListService.clear_all_responder_padv_report_started_status();
					this.responderListService.clear_all_responder_cl_aoa_started_status();

					this.responderListService.clear_current_responder_list();
					this.responderListService.clear_periodic_adv_responder_list();

					this.in_sync_process(false);

					this.initialize_done.next(true);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	finalize() {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Finalizing ...");
			this.restAPI.finalizeRtlsUtil().subscribe(
				() => {
					this.continuousData.disconnect();
					resolve("ok");
				},
				error => {
					this.handle_rest_api_error(error);
					reject("error");
				}
			);
		});
	}

	async start_over() {
		await this.serialPortService.disconnect();
		await this.finalize();
	}

	close_websocket() {
		this.continuousData.disconnect();
	}

	async restart(device_list: Device[], websocket_port: number, rtlsutil_timeout: number, aoa_raw_only: boolean) {
		await this.start_over();
		await this.initialize(device_list, websocket_port, rtlsutil_timeout, aoa_raw_only);
		await this.get_setup_capability();
	}

	get_setup_capability() {
		return new Promise<SetupCapability>(resolve => {
			this.msgForSpinner.next("Getting Setup Capability ...");
			this.restAPI.getSetupCapability().subscribe(
				setup_capability => {
					this.setupCapability.next(setup_capability);
					this.cciEnabled.next(false);
					this.hadmEnabled.next(false);
					this.aoaEnabled.next(false);

					this.msgForSpinner.next(null);
					resolve(setup_capability);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	get_device_list(feature) {
		return new Promise<Device[]>(resolve => {
			// console.log(this.msgForSpinner);
			this.msgForSpinner.next("Getting Device List ...");

			this.restAPI.getDeviceList(feature).subscribe(
				deviceList => {
					this.msgForSpinner.next(null);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	indentify_devices(device_list: Device[], feature) {
		return new Promise<Device[]>(resolve => {
			this.msgForSpinner.next("Identifying Devices ...");

			this.restAPI.indentifyDevices(device_list, feature).subscribe(
				deviceList => {
					this.msgForSpinner.next(null);
					resolve(deviceList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	scan(scan_time: number, selected_feature: string) {
		return new Promise<Responder[]>(resolve => {
			this.msgForSpinner.next(`Scanning (${scan_time} sec) ...`);

			let current_connected_responder_list = this.responderListService.get_connected_responders();
			let current_synced_responder_list = this.responderListService.get_synced_responders();
			let current_periodic_adv_list = this.responderListService.get_current_periodic_adv_list();

			this.restAPI.scan(scan_time).subscribe(
				responderList => {
					if (selected_feature === SupportedFeatures.CL_AOA) {
						responderList = responderList.filter(r => r.support_cl_aoa);
					}

					if (selected_feature !== SupportedFeatures.CL_AOA) {
						// ????
						responderList = responderList.filter((v, i, a) => a.findIndex(t => (t.bd_address === v.bd_address)) === i)
					}

					this.responderListService.update_responder_list(
						responderList,
						current_connected_responder_list,
						current_synced_responder_list,
						current_periodic_adv_list
					);

					this.msgForSpinner.next(null);
					resolve(responderList);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	connect(responder: Responder, connection_interval: number) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Connecting to ${responder.bd_address} ...`);

			this.restAPI.connect(responder, connection_interval).subscribe(
				ret => {
					this.responderListService.update_responder_connection_status(
						responder,
						ret
					);

					this.msgForSpinner.next(null);
					resolve(ret);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	onConnectionStatusChange() {
		return this.connectionStatus.share();
	}

	disconnect(responder: Responder) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Disconnecting ...");

			this.restAPI.disconnect(responder).subscribe(
				ret => {
					// ret.success = !ret.success;

					// this.responderListService.update_responder_connection_status(
					// 	responder,
					// 	ret
					// );

					// this.responderListService.clear_responder_in_view(responder);
					// this.responderListService.update_responder_cci_status(responder, false);
					// this.responderListService.update_responder_hadm_status(responder, false);
					// this.responderListService.update_responder_aoa_status(responder, false);

					this.msgForSpinner.next(null);
					resolve({});
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	async diconnect_all_responders() {
		for (const s of this.responderListService.get_connected_responders()) {
			console.log(s);

			await this.disconnect(s);
		}
	}

	enable_cci(responder: Responder) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Starting CCI ...");

			this.restAPI.enable_cci(responder).subscribe(
				ret => {
					this.responderListService.update_responder_cci_status(
						responder,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	disable_cci(responder: Responder) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling CCI ...");

			this.restAPI.disable_cci(responder).subscribe(
				ret => {
					this.responderListService.update_responder_cci_status(
						responder,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	async disable_all_cci() {
		for (const s of this.responderListService.get_cci_started_responders()) {
			await this.disable_cci(s);
		}
	}

	enable_aoa(responder: Responder, aoa_parameters: AoaParameters) {
		return new Promise(resolve => {
			this.msgForSpinner.next(
				"Setting AoA Parameters and Starting AoA ..."
			);

			this.restAPI.enable_aoa(responder, aoa_parameters).subscribe(
				ret => {
					this.responderListService.update_responder_aoa_status(
						responder,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_aoa(responder: Responder) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling AoA ...");

			this.restAPI.disable_aoa(responder).subscribe(
				ret => {
					this.responderListService.update_responder_aoa_status(
						responder,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}

	async disable_all_aoa() {
		for (const s of this.responderListService.get_aoa_started_responders()) {
			await this.disable_aoa(s);
		}
	}

	sleep(ms) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Sleep for ${ms} [mSec] ...`);
			setTimeout(() => {
				this.msgForSpinner.next(null);
				resolve("OK");
			}, ms);
		});
	}

	async auto_play(
		active_feature: string,
		device_list: Device[],
		websocket_port: number,
		rtlsutil_timeout: number,
		scan_time: number,
		connection_interval: number,
		aoa_parameters: AoaParameters,
		cl_aoa_parameters: ClAoaParameters,
		aoa_raw_only: boolean
	) {
		// console.debug("Auto Play Started");

		let connectionStatus: ConnectionStatus;
		let syncSuccess = false;

		let status = await this.finalize();
		let devices = await this.initialize(
			device_list,
			websocket_port,
			rtlsutil_timeout,
			aoa_raw_only
		);
		let setupCapability = await this.get_setup_capability();
		let responderList = await this.scan(scan_time, active_feature);

		let bestResponder: Responder;

		if ([SupportedFeatures.AOA, SupportedFeatures.AOA_MR].indexOf(active_feature as SupportedFeatures) !== -1) {
			bestResponder = responderList.reduce((prev, current) => prev.rssi > current.rssi ? prev : current);

			connectionStatus = await this.connect(bestResponder, connection_interval);
			// console.debug(`AoA Connection Status : ${connectionStatus.success}`);

			if (connectionStatus.success) {
				this.responderListService.set_responder_in_view(bestResponder);
			}
		}

		if (active_feature === SupportedFeatures.CL_AOA) {
			let cl_aoa_available_responders = responderList.filter(s => s.support_cl_aoa);

			bestResponder = cl_aoa_available_responders.reduce((prev, current) => prev.rssi > current.rssi ? prev : current);

			cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
			await this.sync(bestResponder, cl_aoa_parameters.cl_aoa_scanner, scan_time);

			await this.sleep(2000);

			let synced_responder = this.responderListService.get_synced_responders();
			if (synced_responder.length === 0) {
				await this.term_sync(bestResponder);

				cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
				await this.sync(bestResponder, cl_aoa_parameters.cl_aoa_scanner, scan_time);

				await this.sleep(2000);

				synced_responder = this.responderListService.get_synced_responders();
			}

			syncSuccess = synced_responder.length > 0 ? true : false;
		}

		if ([SupportedFeatures.AOA, SupportedFeatures.AOA_MR].indexOf(active_feature as SupportedFeatures) !== -1
			&& setupCapability.cci_supported && connectionStatus.success) {
			// console.debug(`Enabling CCI for ${bestResponder.bd_address}`);

			var cciStarted = await this.enable_cci(bestResponder);
			await this.sleep(2000);
		}

		if ([SupportedFeatures.AOA, SupportedFeatures.AOA_MR].indexOf(active_feature as SupportedFeatures) !== -1
			&& setupCapability.aoa_supported && connectionStatus.success && !aoa_raw_only) {
			// console.debug(`Enabling AoA for ${bestResponder.bd_address}`);

			var aoaStarted = await this.enable_aoa(bestResponder, aoa_parameters);
			await this.sleep(2000);
		}

		if (active_feature === SupportedFeatures.CL_AOA && syncSuccess) {
			// console.debug(`Enabling Periodic Report for ${bestResponder.bd_address}`);

			await this.enable_periodic_adv_report(bestResponder);

			await this.sleep(2000);

			if (!aoa_raw_only) {

				await this.enable_cl_aoa(bestResponder, cl_aoa_parameters.cl_aoa_config);

				await this.sleep(2000);

			}

		}

		// console.debug("Auto Play Ended");
	}

	update_device_list(deviceList: SelectedDevice[]) {
		this.deviceList.next(deviceList);
	}

	getCurrentConnectionStatus(): ConnectionStatus {
		return this.connectionStatus.getValue();
	}

	in_sync_process(status) {
		this.inSyncProcess.next(status);
	}

	sync(responder: Responder, scanner_options: ClAoaScannerParameters, scan_time: number) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Syncing with ${responder.bd_address} ...`);

			this.in_sync_process(true);

			this.restAPI.sync(responder, scanner_options, scan_time).subscribe(
				ret => {
					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)
		});
	}

	cancel_sync() {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Canceling sync ...`);

			this.restAPI.cancel_sync().subscribe(
				ret => {
					this.in_sync_process(false);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)
		});
	}

	term_sync(responder: Responder) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Terminating Sync ...`);

			this.restAPI.term_sync(responder).subscribe(
				ret => {
					ret.success = !ret.success;

					this.responderListService.update_responder_sync_status(responder, ret);
					this.responderListService.update_padvList_responder_sync_status(responder, ret);

					this.responderListService.update_responder_padv_report_status(responder, false);
					this.responderListService.update_responder_cl_aoa_status(responder, false);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			)

		});
	}

	async term_all_sync() {
		for (const s of this.responderListService.get_synced_responders()) {
			await this.term_sync(s);
		}
	}

	clear_periodic_adv_list() {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Clear Periodic Advertiser List ...`);

			this.restAPI.clear_periodic_adv_list().subscribe(
				ret => {
					if (ret.success) {
						this.responderListService.clear_periodic_adv_responder_list();
					}

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	add_responder_into_periodic_adv_list(responder: Responder) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Add ${responder.bd_address} into Periodic Advertiser List ...`);

			this.restAPI.add_responder_into_periodic_adv_list(responder).subscribe(
				ret => {
					if (ret.success) {
						this.responderListService.add_responder_into_periodic_adv_responder_list(responder);
					}

					this.msgForSpinner.next(null);
					resolve("OK");
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	remove_responder_from_periodic_adv_list(responder: Responder) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Remove ${responder.bd_address} from Periodic Advertiser List ...`);

			this.restAPI.remove_responder_from_periodic_adv_list(responder).subscribe(
				ret => {
					if (ret.success) {
						this.responderListService.remove_responder_from_periodic_adv_responder_list(responder);
					}

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	enable_periodic_adv_report(responder: Responder) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Enabling periodic advertiser report ...`);

			this.restAPI.enable_periodic_adv_report(responder).subscribe(
				ret => {
					this.responderListService.update_responder_padv_report_status(
						responder,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_periodic_adv_report(responder: Responder) {
		return new Promise(resolve => {
			this.msgForSpinner.next(`Disabling periodic advertiser report ...`);

			this.restAPI.disable_periodic_adv_report(responder).subscribe(
				ret => {
					this.responderListService.update_responder_padv_report_status(
						responder,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	enable_cl_aoa(responder: Responder, cl_aoa_config_parameters: ClAoaConfigParameters) {
		return new Promise(resolve => {
			this.msgForSpinner.next("Starting CL AoA ...");

			this.restAPI.enable_cl_aoa(responder, cl_aoa_config_parameters).subscribe(
				ret => {
					this.responderListService.update_responder_cl_aoa_status(
						responder,
						ret.success
					);

					this.msgForSpinner.next(null);
					resolve(ret.success);
				},
				error => {
					this.handle_rest_api_error(error);
				}
			);
		});
	}

	disable_cl_aoa(responder: Responder, cl_aoa_config_parameters: ClAoaConfigParameters) {
		return new Promise((resolve, reject) => {
			this.msgForSpinner.next("Disabling CL AoA ...");

			this.restAPI.disable_cl_aoa(responder, cl_aoa_config_parameters).subscribe(
				ret => {
					this.responderListService.update_responder_cl_aoa_status(
						responder,
						!ret.success
					);

					this.msgForSpinner.next(null);
					resolve(null);
				},
				error => {
					this.handle_rest_api_error(error);
					reject(error);
				}
			);
		});
	}


}
