import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { MatTableDataSource, MatSort, MatSortable } from '@angular/material';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { Configuration } from 'app/interfaces/configuration';
import { Responder } from 'app/interfaces/responder';
import { ToastrService } from 'ngx-toastr';
import { ConfigurationService } from 'app/services/configuration.service';
import { ResponderListService } from "app/services/responder-list.service";
import { environment } from 'environments/environment'

@Component({
	selector: 'periodic-advertiser-list',
	templateUrl: './periodic-advertiser-list.component.html',
	styleUrls: ['./periodic-advertiser-list.component.scss']
})
export class PeriodicAdvertiserListComponent implements OnInit, OnDestroy {
	aoa_raw_only = environment.aoa_raw_only;

	@ViewChild(MatSort, { static: true }) sort: MatSort;

	private _configuration: Configuration;
	public in_sync_process: boolean = false;
	private responder_list: Responder[] = [];
	public all_synced: boolean = false;
	public is_any_responder_synced = false;

	public dataSource = new MatTableDataSource<Responder>([]);

	private configurationServiceOnConfigurationChangeSubscribeObj: any;
	private responderListServiceOnResponderListChangeSubscribeObj: any;
	private rtlsUtilServiceInSyncProcessSubscribeObj: any;

	constructor(
		private toastr: ToastrService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe((configuration) => {
			this._configuration = configuration;
			console.log("Configuration updated in periodic-advertiser-list component");
		});

		this.responderListServiceOnResponderListChangeSubscribeObj = this.responderListService.on_periodic_adv_responder_list_change().subscribe((responderList) => {
			this.responder_list = responderList;
			this.dataSource.data = responderList;

			let synced_responders = this.responder_list.filter(s => s.is_synced);
			this.all_synced = synced_responders.length === this.responder_list.length;
		});

		this.rtlsUtilServiceInSyncProcessSubscribeObj = this.rtlsUtilService.inSyncProcess.subscribe(in_process => {
			this.in_sync_process = in_process;
		});

		this.responderListService.isAnyResponderSynced.subscribe(flag => {
			this.is_any_responder_synced = this.responderListService.get_current_periodic_adv_list().filter(s => s.is_synced).length > 0;
		});

		this.dataSource.sort = this.sort;
		this.dataSource.sortingDataAccessor = (data, attribute) => {
			switch (attribute) {
				case "rssi":
					return Math.abs(data[attribute]);
				default:
					return data[attribute];
			}
		};
		this.dataSource.sort.sort(<MatSortable>{
			id: "rssi",
			start: "asc",
		});
	}

	ngOnDestroy() {
		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.responderListServiceOnResponderListChangeSubscribeObj.unsubscribe();
		this.rtlsUtilServiceInSyncProcessSubscribeObj.unsubscribe();
	}

	sortChange(e) {
		if (e.direction !== "") {
			this.dataSource.sortData(
				this.dataSource.filteredData,
				this.dataSource.sort
			);
		}
	}

	update_view_selection(e, row) {
		e.preventDefault();

		if (row.is_connected) {
			this.responderListService.set_responder_in_view(row);
		}
	}

	getDisplayedColumns(): string[] {
		let displayedColumnsCLAOA = [
			"aRemoveToPerAdvList",
			// "select",
			"bd_address",
			// "rtls_role",
			"rssi",
			"aConnectionHandle",
			"aPeriodicInterval",
			"aTerminateSync",
			"aPerdiocAdvertiserReport",
			"aClAoA",
			"aSyncColor"
		];

		if (this.aoa_raw_only) {
			displayedColumnsCLAOA = displayedColumnsCLAOA.filter(c => c !== 'aClAoA');
		}

		return displayedColumnsCLAOA;
	}

	cleat_per_adv_list() {
		this.rtlsUtilService.clear_periodic_adv_list();
	}

	remove_from_per_adv_list(row) {
		this.rtlsUtilService.remove_responder_from_periodic_adv_list(row);
	}

	async sync_with_next() {
		let responder: Responder = {
			addrType: 0,
			advSID: 0,
			bd_address: "FF:FF:FF:FF:FF:FF",
			in_view: false,
			periodicAdvInt: 0,
			rssi: 0,
			rtls_role: "RESPONDER",
			support_cl_aoa: true
		}

		this._configuration.cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = true;

		await this.rtlsUtilService.sync(
			responder,
			this._configuration.cl_aoa_parameters.cl_aoa_scanner,
			this._configuration.scan_connection.scan_time
		);
	}

	async term_sync(row) {
		await this.rtlsUtilService.term_sync(row);
	}

	cancel_sync() {
		this.rtlsUtilService.cancel_sync();
	}

	perdioc_adv_report(row) {
		if (!('padv_report_started' in row) || row.padv_report_started === false) {
			this.rtlsUtilService.enable_periodic_adv_report(row);
		} else {
			this.rtlsUtilService.disable_periodic_adv_report(row);
		}
	}

	async sync_with_all() {
		let sync_responders_in_list = this.responder_list.filter(s => s.is_synced);
		let attempt = this.responder_list.length + 3;

		while (this.responder_list.length !== sync_responders_in_list.length && attempt > 0) {

			await this.sync_with_next();

			sync_responders_in_list = this.responder_list.filter(s => s.is_synced);
			attempt -= 1;

			if (!this.in_sync_process) {
				continue;
			}

			this.cancel_sync();
		}
	}

	cl_aoa(row) {
		if ("is_synced" in row && row.is_synced) {
			if ("cl_aoa_started" in row && row.cl_aoa_started) {
				this.rtlsUtilService.disable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			} else {
				this.rtlsUtilService.enable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			}
		}
	}

	async term_all_sync() {
		for (const responder of this.responderListService.get_current_periodic_adv_list().filter(s => s.is_synced)) {
			await this.rtlsUtilService.term_sync(responder);
		}
	}
}
