import { NgModule } from '@angular/core';
import { BrowserModule, DomSanitizer, } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule } from "@angular/common/http";
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';


import {
	MatButtonModule,
	MatButtonToggleModule,
	MatIconModule,
	MatIconRegistry,
	MatToolbarModule,
	MatTableModule,
	MatCheckboxModule,
	MatRadioModule,
	MatPaginatorModule,
	MatDialogModule,
	MatTabsModule,
	MatInputModule,
	MatSelectModule,
	MatSortModule,
	MatTooltipModule
} from '@angular/material';

import {
	TiPageTitleModule,
	TiSidenavModule,
	TiToolbarModule,
	TiLabelValueListModule,
	TiFormElementModule,
	TiExpansionListModule,
	TiExpansionListGroupModule
} from '@ti-gui/gui-angular';


import { FlexLayoutModule } from '@angular/flex-layout';
import { NgxSpinnerModule } from 'ngx-spinner';
import { ToastrModule } from 'ngx-toastr';
import { ColorPickerModule } from 'ngx-color-picker';
import { ChartsModule } from 'ng2-charts';
import { MatTableExporterModule } from 'mat-table-exporter';
import { InlineSVGModule } from 'ng-inline-svg';
import { NgTerminalModule } from 'ng-terminal';

import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';
import { DevicesSelectionComponent } from './components/devices-selection/devices-selection.component';
import { DeviceBoxComponent } from './components/device-box/device-box.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { CoordinatorPassiveListComponent } from './components/coordinator-passive-list/coordinator-passive-list.component';
import { ResponderListComponent } from './components/responder-list/responder-list.component';
import { CciChartComponent } from './components/cci-chart/cci-chart.component';
import { LogBoxComponent } from './components/log-box/log-box.component';
import { AoaChartComponent } from './components/aoa-chart/aoa-chart.component';
import { ConfigurationDialogComponent } from './dialogs/configuration-dialog/configuration-dialog.component';
import { ErrorDialogComponent } from './dialogs/error-dialog/error-dialog.component';
import { SpaceViewComponent } from './components/space-view/space-view.component';
import { AoaBarChartComponent } from './components/aoa-bar-chart/aoa-bar-chart.component';
import { FeatureSelectionComponent } from './components/feature-selection/feature-selection.component';
import { PeriodicAdvertiserListComponent } from './components/cl-aoa/periodic-advertiser-list/periodic-advertiser-list.component';
import { TxpowerRssiChartComponent } from './components/cl-aoa/txpower-rssi-chart/txpower-rssi-chart.component';
import { ResponderByAngleChartComponent } from './components/cl-aoa/responder-by-angle-chart/responder-by-angle-chart.component';
import { ForceRefreshDialogComponent } from './dialogs/force-refresh-dialog/force-refresh-dialog.component';
import { SerialPortViewerComponent } from './components/serial-port-viewer/serial-port-viewer.component';
import { ActiveRespondersComponent } from './components/active-responders/active-responders.component';

@NgModule({
	declarations: [
		AppComponent,
		HomeComponent,
		DevicesSelectionComponent,
		DeviceBoxComponent,
		DashboardComponent,
		CoordinatorPassiveListComponent,
		ResponderListComponent,
		CciChartComponent,
		LogBoxComponent,
		AoaChartComponent,
		ConfigurationDialogComponent,
		ErrorDialogComponent,
		SpaceViewComponent,
		AoaBarChartComponent,
		FeatureSelectionComponent,
		PeriodicAdvertiserListComponent,
		TxpowerRssiChartComponent,
		ResponderByAngleChartComponent,
		ForceRefreshDialogComponent,
		SerialPortViewerComponent,
		ActiveRespondersComponent,

	],
	entryComponents: [
		ConfigurationDialogComponent,
		ErrorDialogComponent,
		ForceRefreshDialogComponent
	],
	imports: [
		BrowserModule,
		BrowserAnimationsModule,
		HttpClientModule,
		FormsModule,
		ReactiveFormsModule,
		MatFormFieldModule,

		FlexLayoutModule,
		NgxSpinnerModule,
		ToastrModule.forRoot({
			maxOpened: 1,
			autoDismiss: false
		}),
		ColorPickerModule,
		ChartsModule,
		MatTableExporterModule,
		InlineSVGModule.forRoot({ baseUrl: `http://127.0.0.1:5005` }),
		NgTerminalModule,

		MatButtonModule,
		MatButtonToggleModule,
		MatIconModule,
		MatToolbarModule,
		MatTableModule,
		MatCheckboxModule,
		MatRadioModule,
		MatPaginatorModule,
		MatDialogModule,
		MatTabsModule,
		MatInputModule,
		MatSelectModule,
		MatSortModule,
		MatTooltipModule,

		TiPageTitleModule,
		TiSidenavModule,
		TiToolbarModule,
		TiLabelValueListModule,
		TiFormElementModule,
		TiExpansionListModule,
		TiExpansionListGroupModule,

		AppRoutingModule
	],
	providers: [],
	bootstrap: [AppComponent]
})
export class AppModule {
	constructor(matIconRegistry: MatIconRegistry, domSanitizer: DomSanitizer) {
		matIconRegistry.addSvgIconSet(domSanitizer.bypassSecurityTrustResourceUrl('./assets/images/mdi.svg')); // Or whatever path you placed mdi.svg at
	}
}
