import { Component, OnInit } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";
import { ToastrService } from "ngx-toastr";
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { environment } from 'environments/environment';

@Component({
	selector: "app-feature-selection",
	templateUrl: "./feature-selection.component.html",
	styleUrls: ["./feature-selection.component.scss"]
})
export class FeatureSelectionComponent implements OnInit {
	private env = environment;

	features = [
		{
			name: SupportedFeatures.AOA,
			head: "RTLS Connected",
			description: "",
			selected: false,
			show: this.env.aoa_support
		},
		{
			name: SupportedFeatures.AOA_MR,
			head: "RTLS Connected",
			description: "",
			selected: false,
			show: this.env.aoa_mr_support
		},
		{
			name: SupportedFeatures.CL_AOA,
			head: "RTLS Connectionless",
			description: "",
			selected: false,
			show: this.env.cl_aoa_support
		}
	];

	constructor(private router: Router) { }

	ngOnInit() { }

	is_something_selected(): Boolean {
		let selected_feature = this.features.filter(f => f.selected);
		return selected_feature.length == 1;
	}

	continue() {
		let selected_feature = this.features.filter(f => f.selected);
		this.router.navigate(["/devices-selection-page", { feature: selected_feature[0].name }]);
	}

	XOR(a, b) {
		return (a || b) && !(a && b);
	}
}
