import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/home/home.component';
import { DevicesSelectionComponent } from './components/devices-selection/devices-selection.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { FeatureSelectionComponent } from './components/feature-selection/feature-selection.component';


const routes: Routes = [
	{ path: '', redirectTo: 'home-page', pathMatch: 'full' },
	{ path: 'home-page', component: HomeComponent, data: { title: 'Home', breadcrumb: 'Home' } },
	{ path: 'feature-selection-page', component: FeatureSelectionComponent, data: { title: 'Feature Selection', breadcrumb: 'Feature Selection' } },
	{ path: 'devices-selection-page', component: DevicesSelectionComponent, data: { title: 'Coordinator Selection', breadcrumb: 'Coordinator Selection' } },
	{ path: 'dashboard-page', component: DashboardComponent, data: { title: 'Dashboard', breadcrumb: 'Dashboard' } }
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule { }
