import { Component, OnInit } from '@angular/core';

@Component({
	selector: 'app-force-refresh-dialog',
	templateUrl: './force-refresh-dialog.component.html',
	styleUrls: ['./force-refresh-dialog.component.scss']
})
export class ForceRefreshDialogComponent implements OnInit {

	img_src: string = ""

	constructor() { }

	ngOnInit() {
		// console.log(navigator.platform);

		if (navigator.userAgent.indexOf("Chrome") !== -1) {
			this.img_src = 'assets/images/hard-refresh-browser-chrome.jpg';
		} else if (navigator.userAgent.indexOf("Firefox") !== -1) {
			this.img_src = 'assets/images/hard-refresh-browser-firefox.jpg';
		} else {
			this.img_src = 'hard-refresh-browser-not-supported.png';
		}
	}

}
