import { Injectable } from '@angular/core';
import { ActiveResponder } from 'app/interfaces/active-responder';
import { ConnectionStatus } from 'app/interfaces/connection-status';
import { Coordinator } from 'app/interfaces/coordinator';
import { Responder } from 'app/interfaces/responder';
import { BehaviorSubject, Subject } from 'rxjs';
import { RestApiService } from './rest-api.service';

@Injectable({
	providedIn: 'root'
})
export class ActiveResponderService {
	public msgForSpinner: Subject<string> = new Subject<string>();

	public currentScanResult: BehaviorSubject<Coordinator[]> = new BehaviorSubject<Coordinator[]>([]);

	public activeResponders: BehaviorSubject<ActiveResponder[]> = new BehaviorSubject<ActiveResponder[]>([]);

	private scanResultDataBase = {};

	constructor(private restAPI: RestApiService,
	) { }

	setActiveResponders(responders: ActiveResponder[]) {
		this.activeResponders.next([]);
		this.activeResponders.next(responders);
		this.currentScanResult.next([]);
	}

	updateScanResult(results: Coordinator[], responderIndex: number) {
		this.scanResultDataBase[responderIndex] = results;

		this.currentScanResult.next(results);
	}

	responderSwitch(activeResponderIndex: number) {
		let results = this.scanResultDataBase[activeResponderIndex];

		this.currentScanResult.next(results);
	}

	scan(scanTime: number, activeResponderIndex: number) {
		return new Promise<Coordinator[]>(resolve => {
			this.msgForSpinner.next(`Scanning (${scanTime} sec) ...`);

			let selected_responder = this.activeResponders.getValue()[activeResponderIndex];

			this.restAPI.responder_scan(scanTime, selected_responder).subscribe(
				coordinatorList => {
					this.updateScanResult(coordinatorList, activeResponderIndex);

					this.msgForSpinner.next(null);
					resolve(coordinatorList);
				},
				error => {
					// this.handle_rest_api_error(error);
				}
			);
		});
	}

	connect(coordinator: Coordinator, connection_interval: number, activeResponderIndex: number) {
		return new Promise<ConnectionStatus>(resolve => {
			this.msgForSpinner.next(`Connecting to ${coordinator.addr} ...`);

			let selected_responder = this.activeResponders.getValue()[activeResponderIndex];

			this.restAPI.responder_connect(coordinator, connection_interval, selected_responder).subscribe(
				status => {

					if (status.success) {
						let results = this.scanResultDataBase[activeResponderIndex];
						let new_reesults = results.filter(r => r.addr !== coordinator.addr);
						this.updateScanResult(new_reesults, activeResponderIndex);
					}

					this.msgForSpinner.next(null);
					resolve(status);
				},
				error => {
					// this.handle_rest_api_error(error);
				}
			);
		});
	}

}
