import { Component, Input, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { ActiveResponder } from 'app/interfaces/active-responder';
import { Configuration } from 'app/interfaces/configuration';
import { Coordinator } from 'app/interfaces/coordinator';
import { Responder } from 'app/interfaces/responder';
import { ActiveResponderService } from 'app/services/active-responder.service';
import { ConfigurationService } from 'app/services/configuration.service';

@Component({
	selector: 'active-responders',
	templateUrl: './active-responders.component.html',
	styleUrls: ['./active-responders.component.scss']
})
export class ActiveRespondersComponent implements OnInit {

	_configuration: Configuration;

	selectedResponderIndex: number = -1;

	responderList: ActiveResponder[] = [];

	dataSource = new MatTableDataSource<Coordinator>([]);

	constructor(
		private activeResponderService: ActiveResponderService,
		private configurationService: ConfigurationService
	) { }

	ngOnInit() {
		this.configurationService.onConfigurationChange().subscribe(configuration => this._configuration = configuration);

		this.activeResponderService.activeResponders.subscribe((list) => {
			if (list.length === 0) {
				this.selectedResponderIndex = -1;
				this.responderList = [];
			} else {
				this.responderList = list;
			}
		});

		this.activeResponderService.currentScanResult.subscribe(
			results => this.dataSource.data = results
		);
	}

	getLabel(responder: ActiveResponder): string {
		return `${responder.addr} [ ${responder.port} ]`
	}

	getDisplayedColumns(): string[] {
		return ['c_bd_address', 'c_rssi', 'c_action']
	}

	onSelectionChanged(event) {
		this.selectedResponderIndex = event.value
		this.activeResponderService.responderSwitch(this.selectedResponderIndex);
	}

	scan() {
		this.activeResponderService.scan(this._configuration.scan_connection.scan_time, this.selectedResponderIndex)
	}

	connection(coordinator) {
		this.activeResponderService.connect(coordinator, this._configuration.scan_connection.connection_interval, this.selectedResponderIndex);
	}
}

