import { Component, OnInit, ViewChild, AfterViewInit, ElementRef, Input, OnDestroy } from '@angular/core';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { LogLine } from 'app/interfaces/log-line';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { ConfigurationService } from 'app/services/configuration.service';
import { Configuration } from 'app/interfaces/configuration';
import { SelectedDevice } from 'app/interfaces/selected-device';
import { ResponderListService } from 'app/services/responder-list.service';
import { Responder } from 'app/interfaces/responder';


@Component({
	selector: 'log-box',
	templateUrl: './log-box.component.html',
	styleUrls: ['./log-box.component.scss']
})
export class LogBoxComponent implements OnInit, OnDestroy, AfterViewInit {

	@Input() deviceList: SelectedDevice[];

	@ViewChild(MatSort, { static: true }) sort: MatSort;
	@ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
	@ViewChild("matTable", { static: true }) table: ElementRef;

	_configuration: Configuration;

	logs: LogLine[] = [];

	scroll_enabled = true;

	responder_in_conn_bd_addr: string = null;

	displayedColumns: string[] = ['date_time', 'identifier', 'message'];
	dataSource = new MatTableDataSource<LogLine>(this.logs);

	configurationServiceOnConfigurationChangeSubscribeObj: any;
	rtlsUtilServiceLogMessagesSubscribeOnj: any;
	rtlsUtilServiceInternalLogMessagesSubscribeOnj: any;
	rtlsUtilServiceInitializeDoneSubscribeObj: any;
	rtlsUtilServiceDeviceListSubscribeObj: any;

	normal_bg_color: string = "white";
	warning_bg_color: string = "#ffd5d4";

	activeCoordinator: SelectedDevice;

	constructor(
		private configurationService: ConfigurationService,
		private responderListService: ResponderListService,
		private rtlsUtilService: RtlsUtilService
	) { }

	ngOnInit() {
		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			this._configuration = new_configuration;
		})

		this.rtlsUtilServiceLogMessagesSubscribeOnj = this.rtlsUtilService.logMessages.subscribe(msg => {
			this.updateLogTable(msg);
		})

		this.rtlsUtilServiceInternalLogMessagesSubscribeOnj = this.rtlsUtilService.internalLogMessages.subscribe(msg => {
			this.updateInternalLogTable(msg);
		})

		this.rtlsUtilServiceInitializeDoneSubscribeObj = this.rtlsUtilService.initialize_done.subscribe(() => {
			this.dataSource.data = [];
		})

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(
			deviceList => {
				this.activeCoordinator = deviceList.filter(d => d.rtls_role === "COORDINATOR")[0];

				this.clear_logs();
			}
		);
	}

	ngOnDestroy() {
		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.rtlsUtilServiceLogMessagesSubscribeOnj.unsubscribe();
		this.rtlsUtilServiceInternalLogMessagesSubscribeOnj.unsubscribe();
		this.rtlsUtilServiceInitializeDoneSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
	}

	ngAfterViewInit(): void {
		this.dataSource.sort = this.sort;
		this.dataSource.paginator = this.paginator;
	}

	getCurrentDatetime() {
		let now = new Date();

		// var date = now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate();
		// var time = now.getHours() + ":" + now.getMinutes() + ":" + now.getSeconds() + ":" + now.getMilliseconds();
		var time = [String(now.getHours()).padStart(2, "0"),
		String(now.getMinutes()).padStart(2, "0"),
		String(now.getSeconds()).padStart(2, "0"),
		String(now.getMilliseconds()).padEnd(3, "0")].join(':');

		// var dateTime = date + ' ' + time;

		// return dateTime;
		return time;
	}

	updateLog(logLine: LogLine) {
		if (this.dataSource.data.length >= this._configuration.general.max_log_messages) {
			this.dataSource.data = [logLine]
		} else {
			this.dataSource.data.push(logLine);
		}
		this.dataSource.data = this.dataSource.data.slice();

		if (this.scroll_enabled) {
			setTimeout(() => {
				if (this.paginator.hasNextPage()) {
					this.paginator.nextPage();
				} else {
					document.querySelector("#log-box-table-container").scrollBy(0, 10000);
				}
			}, 10);
		}
	}

	updateLogTable(recv_data) {
		let device = this.deviceList.find(d => d.bd_address === recv_data.identifier);

		let log_line: LogLine = {
			date_time: this.getCurrentDatetime(),
			identifier: recv_data.identifier,
			message: "",
			row_color: "black",
			row_backcolor: this.normal_bg_color
		}

		switch (recv_data.message.command) {
			case "RTLS_CMD_RESET_DEVICE":
				if (recv_data.message.type === "AsyncReq") {
					log_line.message = 'Device Reseted';
				}
				break;
			case "RTLS_CMD_IDENTIFY":
				log_line.message = 'Identify Responde';
				break;
			case "RTLS_CMD_SCAN":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Scan started successfully';
					} else {
						log_line.message = 'Fail to start scan';
					}
				} else {
					log_line.message = `Responder ${recv_data.message.payload.addr} found with rssi ${recv_data.message.payload.rssi}`;
				}
				break;
			case "RTLS_CMD_SCAN_STOP":
				if (recv_data.message.payload.status === "RTLS_SUCCESS") {
					log_line.message = 'Scan stopped successfully';
				} else {
					log_line.message = 'Fail to stop scan';
				}
				break;
			case "RTLS_CMD_CONNECT":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Connection started successfully';
					} else {
						log_line.message = 'Fail to start connection';
					}
				} else {
					switch (recv_data.message.payload.status) {
						case "RTLS_SUCCESS":
							let connHandle = recv_data.message.payload.connHandle;

							if (this.responder_in_conn_bd_addr && connHandle !== 255) {
								log_line.message = 'Connection success';

								this.responderListService.update_responder_connection_status_by_bd_addr(
									this.responder_in_conn_bd_addr,
									{ success: true, conn_hdl: connHandle }
								)

								this.responderListService.set_responder_in_view_by_conn_hdl(connHandle);
							}

							break;
						case "RTLS_FAIL":
							log_line.message = 'Connection fail';
							log_line.row_backcolor = this.warning_bg_color;
							break;
						case "RTLS_LINK_TERMINATED":
							log_line.message = 'Connection fail or Disconnection success';

							if (this.activeCoordinator.bd_address === recv_data.identifier) {
								let conn_hdl = recv_data.message.payload.connHandle;

								let connected_responders = this.responderListService.get_connected_responders();
								let responder_by_conn_hndl = connected_responders.filter(r => r.connection_handle === conn_hdl)[0];

								if (responder_by_conn_hndl !== undefined) {
									// this.responderListService.update_responder_hadm_status(responder_by_conn_hndl, false);
									this.responderListService.update_responder_aoa_status(responder_by_conn_hndl, false);
									this.responderListService.update_responder_cci_status(responder_by_conn_hndl, false);
									this.responderListService.update_responder_connection_status(responder_by_conn_hndl, { 'success': false, 'conn_hdl': -1 });

									connected_responders = this.responderListService.get_connected_responders();

									if (responder_by_conn_hndl.in_view && connected_responders.length > 0) {
										this.responderListService.set_responder_in_view(connected_responders[0]);
									}
								}
							}

							this.responderListService.clear_connection_role_for(recv_data.identifier);

							break;
						case "RTLS_LINK_ESTAB_FAIL":
							log_line.message = 'Failed to establish connection';
							break;
					}
				}
				break;
			case "RTLS_CMD_TERMINATE_LINK":
				log_line.message = 'Disconnection started successfully';
				// log_line.row_backcolor = this.warning_bg_color;
				break;
			case "RTLS_CMD_CONN_PARAMS":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Connection parameters set successfully';
					} else {
						log_line.message = 'Fail to set connection parameters';
					}
				} else {
					log_line.message = 'Receive connection parameters';

					if (this.activeCoordinator.bd_address === recv_data.identifier) {
						let connection_role = recv_data.message.payload.connRole == 4 ? "CENTRAL" : "PERIPHERAL";

						this.responder_in_conn_bd_addr = recv_data.message.payload.peerAddr;

						let current_connected_responder_list = this.responderListService.get_connected_responders();
						let current_synced_responder_list = this.responderListService.get_synced_responders();
						let current_periodic_adv_list = this.responderListService.get_current_periodic_adv_list();
						let responderList = this.responderListService.get_current_responder_list();

						if (!this.responderListService.is_responder_in_responder_list(this.responder_in_conn_bd_addr)) {
							let responder: Responder = {
								in_view: false,
								bd_address: this.responder_in_conn_bd_addr,
								rtls_role: "RESPONDER",
								rssi: 0,
								addrType: 0,
								advSID: 255,
								periodicAdvInt: -1,
								support_cl_aoa: true,
								cci_started: false,
								aoa_started: false,
								cl_aoa_started: false,
								connection_handle: recv_data.message.payload.connHandle,
								connection_role: connection_role
							}

							responderList.push(responder);
						} else {
							responderList.forEach(responder => {
								if (responder.bd_address === this.responder_in_conn_bd_addr) {
									responder.connection_role = connection_role;
								}
							});
						}

						this.responderListService.update_responder_list(
							responderList,
							current_connected_responder_list,
							current_synced_responder_list,
							current_periodic_adv_list
						);
					}
				}
				break;
			case "RTLS_CMD_CONN_INFO":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'CCI started/stopped successfully';
					} else {
						log_line.message = 'Fail to start/stop CCI';
					}
				}
				break;
			case "RTLS_EVT_CONN_INFO":
				log_line.message = `CCI: Channel=${recv_data.message.payload.channel}, RSSI=${recv_data.message.payload.rssi}`;
				break;
			case "RTLS_CMD_AOA_SET_PARAMS":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Set AoA parameters successfully';
					} else {
						log_line.message = 'Fail to set AoA parameters';
					}
				}
				break;
			case "RTLS_CMD_AOA_ENABLE":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'AoA started/stopped successfully';
					} else {
						log_line.message = 'Fail to start/stop AoA';
					}
				}
				break;
			case "RTLS_CMD_AOA_RESULT_ANGLE":
				log_line.message = `AoA: Angle=${recv_data.message.payload.angle.toFixed(2)}, Ant=${recv_data.message.payload.antenna}, Channel=${recv_data.message.payload.channel}, RSSI=${recv_data.message.payload.rssi.toFixed(2)}`;
				break;
			case "RTLS_CMD_HADM_SET_PARAMS":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Set HADM parameters successfully';
					} else {
						log_line.message = 'Fail to set HADM parameters';
					}
				}
				break;
			case "RTLS_CMD_HADM_ENABLE":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'HADM started/stopped successfully';
					} else {
						log_line.message = 'Fail to start/stop HADM';
					}
				}
				break;
			case "RTLS_CMD_HADM_RESULT_DIST":
				log_line.message = `HADM: Distance=${recv_data.message.payload.distance.toPrecision(3)} m, RSSI=${recv_data.message.payload.rssi.toFixed(2)} dBm`;
				break;
			case "RTLS_CMD_CREATE_SYNC":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Sync request successfully';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_CMD_CREATE_SYNC_CANCEL":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Cancel sync request successfully';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_CMD_TERMINATE_SYNC":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Request terminate sync successfully';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_EVT_TERMINATE_SYNC":
				log_line.message = 'Terminate sync successfully';
				break;
			case "RTLS_EVT_SYNC_EST":
				let bd_address = recv_data.message.payload.advAddress;
				let sync_hndl = recv_data.message.payload.syncHandle;
				let status = recv_data.message.payload.status;

				switch (status) {
					case "RTLS_SUCCESS":
						this.responderListService.update_responder_sync_status_by_bd_addr(
							bd_address,
							true,
							sync_hndl,
							!this._configuration.cl_aoa_parameters.cl_aoa_scanner.reporting_initially,
							this._configuration.general.color_schema[parseInt(sync_hndl) + 1]
						);
						this.responderListService.update_padvList_responder_sync_status_by_bd_addr(
							bd_address,
							true,
							sync_hndl,
							!this._configuration.cl_aoa_parameters.cl_aoa_scanner.reporting_initially,
							this._configuration.general.color_schema[parseInt(sync_hndl) + 1]
						);

						log_line.message = `CL AOA: Sync established with ${bd_address} sync handle ${sync_hndl}`;

						this.rtlsUtilService.in_sync_process(false);
						break;
					case "RTLS_SYNC_CANCELED_BY_HOST":
						// this.responderListService.update_responder_sync_status_by_conn_hdl(sync_hndl, false);
						// this.responderListService.update_padvList_responder_sync_status_by_conn_hdl(sync_hndl, false);

						log_line.message = `CL AOA: Sync canceled with status ${status} sync handle ${sync_hndl}`;
						log_line.row_backcolor = this.warning_bg_color;

						this.rtlsUtilService.in_sync_process(false);
						break;
					default:
						break;
				}

				break;
			case "RTLS_EVT_SYNC_LOST":
				let conn_hdl = recv_data.message.payload.syncHandle;

				this.responderListService.update_responder_sync_status_by_conn_hdl(conn_hdl, false);
				this.responderListService.update_padvList_responder_sync_status_by_conn_hdl(conn_hdl, false);

				this.rtlsUtilService.in_sync_process(false);

				log_line.message = `CL AOA: Sync with connection handle ${conn_hdl} lost`;
				log_line.row_backcolor = this.warning_bg_color;
				break;
			case "RTLS_CMD_PERIODIC_RECEIVE_ENABLE":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Periodic report enabled / disabled successfully';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_EVT_PERIODIC_ADV_RPT":
				log_line.message = `CL AOA: TxPower=${recv_data.message.payload.txPower} dBm, RSSI=${recv_data.message.payload.rssi.toFixed(2)} dBm, DataLen=${recv_data.message.payload.dataLen}`;
				break;
			case "RTLS_CMD_ADD_DEVICE_ADV_LIST":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Responder successfully added into periodic advertiser list';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_CMD_REMOVE_DEVICE_ADV_LIST":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Responder successfully removed from periodic advertiser list';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_CMD_CLEAR_ADV_LIST":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'Periodic advertiser list clear successfully';
					} else {
						// TBD
					}
				}
				break;
			case "RTLS_CMD_CL_AOA_ENABLE":
				if (recv_data.message.type === "SyncRsp") {
					if (recv_data.message.payload.status === "RTLS_SUCCESS") {
						log_line.message = 'CL AoA started/stopped successfully';
					} else {
						log_line.message = 'Fail to start/stop CL AoA';
					}
				}
				break;
			case "RTLS_EVT_CL_AOA_ENABLE":
				if (recv_data.message.payload.status === "RTLS_SUCCESS") {
					log_line.message = `CL AoA started/stopped successfully with handle ${recv_data.message.payload.syncHandle}`;
				} else {
					log_line.message = 'Fail to start/stop CL AoA';
				}
				break;
			case "RTLS_CMD_CL_AOA_RESULT_ANGLE":
				log_line.message = `CL AoA: Angle=${recv_data.message.payload.angle.toFixed(2)}, Ant=${recv_data.message.payload.antenna}, Channel=${recv_data.message.payload.channel}, RSSI=${recv_data.message.payload.rssi.toFixed(2)}`;
				break;
			case "RTLS_EVT_DEBUG":
				if (recv_data.message.type === "AsyncReq") {
					log_line.message = `DEBUG: Message="${recv_data.message.payload.debug_string}", Value=${recv_data.message.payload.debug_value}`;
					log_line.row_backcolor = this.warning_bg_color;
				}
				break;
			case "RTLS_EVT_ERROR":
				if (recv_data.message.type === "AsyncReq") {
					log_line.message = `ERROR: Error as reason of last action`;
					log_line.row_backcolor = this.warning_bg_color;
				}
				break;
			case "RTLS_EVT_ASSERT":
				if (recv_data.message.type === "AsyncReq") {
					log_line.message = `ASSERT: Cause="${recv_data.message.payload.cause}"`;
					log_line.row_backcolor = this.warning_bg_color;
				}
				break;
			default:
				break;
		}

		this.updateLog(log_line);
	}

	updateInternalLogTable(msg) {
		let log_line: LogLine = {
			date_time: this.getCurrentDatetime(),
			identifier: "RTLS UI",
			message: msg,
			row_color: "black",
			row_backcolor: this.normal_bg_color
		}

		this.updateLog(log_line);
	}

	getPaginatorData(e) {
		document.querySelector("#log-box-table-container").scrollBy(0, 10000);
	}

	clickOnRow() {
		this.scroll_enabled = false;
	}

	clear_logs() {
		this.dataSource.data = []
		this.dataSource.data = this.dataSource.data.slice();
	}
}
