import { Injectable } from '@angular/core';
import { Observable, defer } from 'rxjs';
import { BehaviorSubject } from "rxjs";

@Injectable({
	providedIn: 'root'
})
export class SerialPortService {

	private comport: any;
	private comport_reader: any;

	public baudrates: number[] = [115200];
	public comport_connection_status: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public comport_output: BehaviorSubject<string> = new BehaviorSubject<string>("");
	public clear_terminal: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

	constructor() { }

	is_supported(): boolean {
		return 'serial' in window.navigator
	}

	is_comport_connected(): boolean {
		return this.comport_connection_status.getValue() === true;
	}

	async connect(selected_baudrate) {

		let nav: any;
		nav = window.navigator;

		this.comport = await nav.serial.requestPort();

		await this.comport.open({ baudRate: selected_baudrate });


		// this.decoder = new TextDecoderStream();
		// this.port.readable.pipeTo(this.decoder.writable);
		// this.port_reader = this.decoder.readable.getReader();

		this.comport_reader = this.comport.readable.getReader();

		// console.log(this.comport);

		// // while (this.port.readable) {
		// // 	this.port_reader = this.port.readable.getReader();
		// // }

		const r = defer(async () => {
			let ret = await this.serial_reader();
			return ret
		});

		const monitor = Observable
			.interval(1000)
			.switchMap(() => r)
			.takeWhile(() => this.comport_connection_status.getValue() == true)
			.subscribe((ret) => {
				const textDecoder = new TextDecoder();
				let current = textDecoder.decode(ret[0]);

				// console.log(current);

				this.comport_output.next(current);

				// current = current.replaceAll('\u001b[2K', '+n+\n');
				// this.output += current

				// console.log(ret[0], current);


				// this.output += ret[0]
				// console.log(typeof(this.output));

			});


		this.comport_connection_status.next(true);
	}

	async disconnect() {

		// console.log(this.port.readable);
		// this.port.readable.locked = false;
		// console.log(this.port.readable);

		// console.log(this.port_reader);
		if (this.is_comport_connected()) {

			this.comport_connection_status.next(false);

			await this.comport_reader.cancel();

			await this.comport_reader.releaseLock();

			await this.comport.close();

			this.clear_terminal.next(true);
		}
	}

	async serial_reader() {
		const { value, done } = await this.comport_reader.read();
		// console.log(value);

		if (done) {
			// console.log('[readLoop] DONE', done);

			await this.comport_reader.releaseLock();
		}

		return [value, done]
	}

}
