import { Injectable, OnInit } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { Responder } from "app/interfaces/responder";
import { ConfigurationService } from './configuration.service';
import { Configuration } from 'app/interfaces/configuration';
import { filter } from "rxjs-compat/operator/filter";

@Injectable({
	providedIn: "root"
})
export class ResponderListService implements OnInit {
	public responderList: BehaviorSubject<Responder[]> = new BehaviorSubject<Responder[]>([]);
	public periodicAdvResponderList: BehaviorSubject<Responder[]> = new BehaviorSubject<Responder[]>([]);

	public isAnyResponderConnected: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderSynced: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderCciStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderAoaStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderHadmStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderPadvReportStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
	public isAnyResponderClAoaStarted: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

	constructor(
	) { }

	ngOnInit() {
	}

	on_responder_list_change() {
		return this.responderList.share();
	}

	update_responder_list(
		responder_list: Responder[],
		previous_connected_responder_list?: Responder[],
		previous_synced_responder_list?: Responder[],
		current_periodic_adv_list?: Responder[]
	) {
		if (previous_connected_responder_list.length > 0) {
			previous_connected_responder_list.forEach(conn_responder => {
				// Filtering out all responder that already connected in scan list
				responder_list = responder_list.filter(s => s.bd_address !== conn_responder.bd_address)
			})

			responder_list.push(...previous_connected_responder_list);
		}

		if (previous_synced_responder_list.length > 0) {
			previous_synced_responder_list.forEach(synced_responder => {
				// Filtering out all responder that already synced in scan list
				responder_list = responder_list.filter(s => s.bd_address !== synced_responder.bd_address)
			})

			responder_list.push(...previous_synced_responder_list);
		}

		if (current_periodic_adv_list.length > 0) {
			current_periodic_adv_list.forEach(padv_list_responder => {
				// Filtering out all responder that already synced in periodic advetizer list
				responder_list = responder_list.filter(s => s.bd_address !== padv_list_responder.bd_address)
			})
		}

		this.responderList.next(responder_list);

		this.check_is_any_responder_connected();
		this.check_is_any_responder_cci_started();
		this.check_is_any_responder_hadm_started();
		this.check_is_any_responder_aoa_started();
		this.check_is_any_responder_padv_report_started();
		this.check_is_any_responder_cl_aoa_started();
	}

	get_current_responder_list(): Responder[] {
		return Array.from(this.responderList.getValue());
	}

	clear_current_responder_list() {
		this.responderList.next([]);
	}

	is_responder_in_responder_list(bd_addr: string): boolean {
		let current_responder_list = this.responderList.getValue();
		let found_responders = current_responder_list.filter(r => r.bd_address == bd_addr);
		return found_responders.length > 0
	}

	// Connection Area

	update_responder_connection_status(responder: Responder, status: any) {
		if (responder === undefined) return;

		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.is_connected = status.success;
				s.connection_handle = status.conn_hdl;

				if (status.success === false) { this.clear_connection_role_for(responder.bd_address); }
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_connected();
	}

	update_responder_connection_status_by_bd_addr(bd_address: string, status: any) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === bd_address) {
				s.is_connected = status.success;
				s.connection_handle = status.conn_hdl;
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_connected();
	}

	update_responder_connection_status_by_conn_hdl(conn_hdl: number, status: any) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.is_connected = status.success;
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_connected();
	}

	get_connected_responders() {
		return this.responderList.getValue().filter(s => s.is_connected);
	}

	check_is_any_responder_connected() {
		let connectd_responder = this.get_connected_responders();
		this.isAnyResponderConnected.next(connectd_responder.length > 0);
	}

	clear_all_responder_connection_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.is_connected = false;
		});
		this.responderList.next(current_responder_list);
		this.check_is_any_responder_connected();
	}

	clear_connection_role_for(reqRespBdAddr: string) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(r => {
			if (r.bd_address === reqRespBdAddr) {
				r.connection_role = '';
			}
		})

		this.responderList.next(current_responder_list);
	}

	// End Connection Area

	// Sync Area

	update_responder_sync_status(responder: Responder, status: any) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.is_synced = status.success;
				s.connection_handle = status.conn_hdl;
				if (status.success === false) { s.color = 'white'; }
				return false;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	update_responder_sync_status_by_bd_addr(bd_address: string, is_synced: boolean, conn_hdl: number, padv_report_started?: boolean, color?: string) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === bd_address) {
				s.is_synced = is_synced;

				s.connection_handle = conn_hdl;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	update_responder_sync_status_by_conn_hdl(conn_hdl: number, is_synced: boolean, padv_report_started?: boolean, color?: string) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.is_synced = is_synced;

				s.connection_handle = is_synced ? conn_hdl : -1;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	get_synced_responders(): Responder[] {
		return [
			...this.responderList.getValue().filter(s => s.is_synced && s.advSID !== 255),
			...this.periodicAdvResponderList.getValue().filter(s => s.is_synced && s.advSID !== 255)
		]
	}

	check_is_any_responder_synced() {
		let synced_responders = this.get_synced_responders();
		this.isAnyResponderSynced.next(synced_responders.length > 0);
	}

	clear_all_responder_sync_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.is_synced = false;
		});
		this.responderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	// End of Sync Area

	// View Area

	clear_responder_in_view(responder: Responder) {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s === responder) {
				s.in_view = false;
			}
		});
		this.responderList.next(current_responder_list);
	}

	clear_all_responder_in_view() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.in_view = false;
		});
		this.responderList.next(current_responder_list);
	}

	is_any_responder_in_view() {
		return this.responderList.getValue().filter(s => s.in_view).length > 0;
	}

	set_responder_in_view(responder: Responder) {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s === responder) {
				s.in_view = true;
			} else {
				s.in_view = false;
			}
		});
		this.responderList.next(current_responder_list);
	}


	set_responder_in_view_by_conn_hdl(conn_hdl: number) {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.in_view = true;
			} else {
				s.in_view = false;
			}
		});
		this.responderList.next(current_responder_list);
	}

	get_responder_in_view(): Responder {
		let current_responder_list = this.responderList.getValue();
		return current_responder_list.find(s => s.in_view);
	}


	// End View Area

	// CCI Area

	update_responder_cci_status(responder: Responder, status: boolean) {
		if (responder === undefined) return;

		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.cci_started = status;
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_cci_started();
	}

	get_cci_started_responders() {
		let connectd_responder = this.get_connected_responders();
		return connectd_responder.filter(s => s.cci_started);
	}

	check_is_any_responder_cci_started() {
		let cci_started_responders = this.get_cci_started_responders();
		this.isAnyResponderCciStarted.next(cci_started_responders.length > 0);
	}

	clear_all_responder_cci_started_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.cci_started = false;
		});
		this.responderList.next(current_responder_list);
		this.check_is_any_responder_cci_started();
	}

	// End CCI Area

	// AoA Area

	update_responder_aoa_status(responder: Responder, status: boolean) {
		if (responder === undefined) return;

		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.aoa_started = status;
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_aoa_started();
	}

	get_aoa_started_responders() {
		let connectd_responder = this.get_connected_responders();
		return connectd_responder.filter(s => s.aoa_started);
	}

	check_is_any_responder_aoa_started() {
		let aoa_started_responders = this.get_aoa_started_responders();
		this.isAnyResponderAoaStarted.next(aoa_started_responders.length > 0);
	}

	clear_all_responder_aoa_started_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.aoa_started = false;
		});
		this.responderList.next(current_responder_list);
		this.check_is_any_responder_aoa_started();
	}

	// End AoA Area

	// HADM Area

	update_responder_hadm_status(responder: Responder, status: boolean) {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.hadm_started = status;
				return;
			}
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_hadm_started();
	}

	get_hadm_started_responders() {
		let connectd_responder = this.get_connected_responders();
		return connectd_responder.filter(s => s.hadm_started);
	}

	check_is_any_responder_hadm_started() {
		let hadm_started_responders = this.get_hadm_started_responders();
		this.isAnyResponderHadmStarted.next(hadm_started_responders.length > 0);
	}

	clear_all_responder_hadm_started_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => {
			s.hadm_started = false;
		});
		this.responderList.next(current_responder_list);
		this.check_is_any_responder_hadm_started();
	}

	// End HADM Area

	// Periodic Advertiser List Area

	on_periodic_adv_responder_list_change() {
		return this.periodicAdvResponderList.share();
	}

	get_current_periodic_adv_list() {
		return this.periodicAdvResponderList.getValue();
	}

	update_padvList_responder_sync_status(responder: Responder, status: any) {
		let current_responder_list = this.periodicAdvResponderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.is_synced = status.success;
				s.connection_handle = status.conn_hdl;
				if (status.success === false) { s.color = 'white'; }
				return false;
			}
		});

		this.periodicAdvResponderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	update_padvList_responder_sync_status_by_bd_addr(bd_address: string, is_synced: boolean, conn_hdl: number, padv_report_started?: boolean, color?: string) {
		let current_responder_list = this.periodicAdvResponderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === bd_address) {
				s.is_synced = is_synced;

				s.connection_handle = conn_hdl;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.periodicAdvResponderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	update_padvList_responder_sync_status_by_conn_hdl(conn_hdl: number, is_synced: boolean, padv_report_started?: boolean, color?: string) {
		let current_responder_list = this.periodicAdvResponderList.getValue();

		current_responder_list.forEach(s => {
			if (s.connection_handle === conn_hdl) {
				s.is_synced = is_synced;

				s.connection_handle = is_synced ? conn_hdl : -1;

				if (padv_report_started !== undefined) {
					s.padv_report_started = padv_report_started
				}

				s.cl_aoa_started = false;

				s.color = is_synced && color !== undefined ? color : 'white';

				return false;
			}
		});

		this.periodicAdvResponderList.next(current_responder_list);
		this.check_is_any_responder_synced();
	}

	add_responder_into_periodic_adv_responder_list(responder: Responder) {
		let periodic_adv_responder_list = this.periodicAdvResponderList.getValue();
		let responder_list = this.responderList.getValue();

		responder_list = responder_list.filter(s => s.bd_address !== responder.bd_address);
		periodic_adv_responder_list.push(responder);

		this.responderList.next(responder_list);
		this.periodicAdvResponderList.next(periodic_adv_responder_list);
	}

	remove_responder_from_periodic_adv_responder_list(responder: Responder) {
		let periodic_adv_responder_list = this.periodicAdvResponderList.getValue();

		periodic_adv_responder_list = periodic_adv_responder_list.filter(s => s.bd_address !== responder.bd_address);

		this.periodicAdvResponderList.next(periodic_adv_responder_list);
	}

	clear_periodic_adv_responder_list() {
		this.periodicAdvResponderList.next([]);
	}

	// End Periodic Advertiser List Area

	// Periodic Advertiser Report

	update_responder_padv_report_status(responder: Responder, status: boolean) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.responderList.next(current_responder_list);

		let current_padv_list_responder_list = this.periodicAdvResponderList.getValue();

		current_padv_list_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.periodicAdvResponderList.next(current_padv_list_responder_list);

		this.check_is_any_responder_padv_report_started();
	}

	update_responder_padv_report_status_by_handle(sync_handle: number, status: boolean) {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.connection_handle === sync_handle) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.responderList.next(current_responder_list);

		let current_padv_list_responder_list = this.periodicAdvResponderList.getValue();

		current_padv_list_responder_list.forEach(s => {
			if (s.connection_handle === sync_handle) {
				s.padv_report_started = status;
				return false;
			}
		});

		this.periodicAdvResponderList.next(current_padv_list_responder_list);

		this.check_is_any_responder_padv_report_started();
	}

	get_padv_report_started_responders() {
		let synced_responder = this.get_synced_responders();

		return synced_responder.filter(s => s.padv_report_started);
	}

	check_is_any_responder_padv_report_started() {
		let padv_report_started_responders = this.get_padv_report_started_responders();

		this.isAnyResponderPadvReportStarted.next(padv_report_started_responders.length > 0);
	}

	clear_all_responder_padv_report_started_status() {
		let current_responder_list = this.responderList.getValue();
		current_responder_list.forEach(s => { s.padv_report_started = false; });
		this.responderList.next(current_responder_list);

		let current_padv_list_responder_list = this.periodicAdvResponderList.getValue();
		current_padv_list_responder_list.forEach(s => { s.padv_report_started = false; });
		this.periodicAdvResponderList.next(current_padv_list_responder_list);

		this.check_is_any_responder_padv_report_started();
	}

	// End Periodic Advertiser Report

	// CL AoA Area

	update_responder_cl_aoa_status(responder: Responder, status: boolean) {
		let update_done = false;

		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			if (s.bd_address === responder.bd_address) {
				s.cl_aoa_started = status;
				update_done = true;
				return false;
			}
		});

		if (update_done) {
			this.responderList.next(current_responder_list);
		} else {
			let padv_responder_list = this.periodicAdvResponderList.getValue();

			padv_responder_list.forEach(s => {
				if (s.bd_address === responder.bd_address) {
					s.cl_aoa_started = status;
					update_done = true;
					return false;
				}
			});

			if (update_done) {
				this.periodicAdvResponderList.next(padv_responder_list);
			}
		}

		this.check_is_any_responder_cl_aoa_started();
	}

	get_cl_aoa_started_responders() {
		let synced_responder = this.get_synced_responders();
		return synced_responder.filter(s => s.cl_aoa_started);
	}

	check_is_any_responder_cl_aoa_started() {
		let cl_aoa_started_responders = this.get_cl_aoa_started_responders();
		this.isAnyResponderClAoaStarted.next(cl_aoa_started_responders.length > 0);
	}

	clear_all_responder_cl_aoa_started_status() {
		let current_responder_list = this.responderList.getValue();

		current_responder_list.forEach(s => {
			s.cl_aoa_started = false;
		});

		this.responderList.next(current_responder_list);
		this.check_is_any_responder_cl_aoa_started();
	}

	// End CL AoA Area
}
