import { Component, OnInit, Input, OnDestroy, ViewChild } from "@angular/core";
import { ContinuousDataService } from "app/services/continuous-data.service";
import * as pluginDataLabels from "chartjs-plugin-datalabels";
import { Label, Color, BaseChartDirective } from "ng2-charts";
import { ChartOptions, ChartType, ChartDataSets } from "chart.js";
import { SelectedDevice } from "app/interfaces/selected-device";
import { Configuration } from "app/interfaces/configuration";
import { ConfigurationService } from "app/services/configuration.service";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { MatDialog } from "@angular/material";
import { ConfigurationDialogComponent } from "app/dialogs/configuration-dialog/configuration-dialog.component";
import { ResponderListService } from "app/services/responder-list.service";
import { Responder } from "app/interfaces/responder";
import { ConfigurationType } from 'app/enums/configuration-type.enum';
import { SupportedFeatures } from 'app/enums/supported-features.enum';


@Component({
	selector: 'aoa-bar-chart',
	templateUrl: './aoa-bar-chart.component.html',
	styleUrls: ['./aoa-bar-chart.component.scss']
})
export class AoaBarChartComponent implements OnInit {
	_deviceList: SelectedDevice[];

	@ViewChild(BaseChartDirective, { static: false })
	public chart: BaseChartDirective;

	public barChartOptions: ChartOptions = {
		responsive: true,
		maintainAspectRatio: false,

		// We use these empty structures as placeholders for dynamic theming.
		scales: {
			xAxes: [
				{
					scaleLabel: {
						display: true,
						labelString: "Channels",
						fontColor: "red"
					}
				}
			],
			yAxes: [
				{
					// ticks: {
					// 	min: 0,
					// 	max: 100,
					// 	stepSize: 10
					// },
					scaleLabel: {
						display: true,
						labelString: "Angle",
						fontColor: "red"
					}
				}
			]
		},
		plugins: {
			datalabels: {
				anchor: "end",
				align: "end"
			}
		},
		tooltips: {
			enabled: false
		}
	};
	public barChartLabels: Label[];
	public barChartType: ChartType = "bar";
	public barChartLegend = false;
	public barChartPlugins = [pluginDataLabels];
	public barChartColors: Color[] = [];
	public barChartData: ChartDataSets[];

	private scale_min: number;
	private scale_max: number;
	private scale_step: number;

	private configurationServiceOnConfigurationChangeSubscribeObj: any;
	private continuousDataIsConnectedSubscribeObj: any;
	private rtlsUtilServiceInitializeDoneSubscribeObj: any;
	private rtlsUtilServiceDeviceListSubscribeObj: any;
	private responderListServiceOnResponderListChangeSubscribeObj: any;
	private responderListServiceIsAnyResponderCciStartedSubscribeObj: any;

	private aoa_enabled = false;
	private auto_scale_enabled = false;
	private active_responder: Responder;

	constructor(
		private continuousData: ContinuousDataService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		public dialog: MatDialog,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		console.info("ngOnInit of aoa-bar-chart component");

		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService
			.onConfigurationChange()
			.subscribe(new_configuration => {
				this.barChartLabels = new_configuration.aoa_parameters.aoa_display_channels;

				this.scale_min =
					new_configuration.general.aoa_bar_graph_parameters.scale.min;
				this.scale_max =
					new_configuration.general.aoa_bar_graph_parameters.scale.max;
				this.scale_step =
					new_configuration.general.aoa_bar_graph_parameters.scale.step;

				this.update_scale();
			});

		this.continuousDataIsConnectedSubscribeObj = this.continuousData
			.isConnected()
			.subscribe(event => {
				if (event == "connected") {
					console.log("AoA Bar chart subscribe to flow data");
					this.continuousData.data.subscribe(
						recv_data => {
							if (
								recv_data &&
								recv_data.message.command ===
								"RTLS_CMD_AOA_RESULT_ANGLE"
							) {
								setTimeout(() => {
									this.updateChart2(recv_data);
								}, 15);
							}
						},
						error => {
							console.log(error);
						}
					);
				}
			});

		this.rtlsUtilServiceInitializeDoneSubscribeObj = this.rtlsUtilService.initialize_done.subscribe(
			() => {
				this.initChart();
			}
		);

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(
			deviceList => {
				// console.log(deviceList);
				this._deviceList = deviceList;

				this.initChart();
			}
		);

		this.responderListServiceOnResponderListChangeSubscribeObj = this.responderListService.on_responder_list_change().subscribe(responderList => {
			// console.log(responderList);
			if (this.responderListService.is_any_responder_in_view()) {
				let responders_in_view = responderList.filter(s => s.in_view);
				if (responders_in_view[0] !== this.active_responder) {
					this.initChart();
				}
				this.active_responder = responders_in_view[0];
			}
		});

		this.responderListServiceIsAnyResponderCciStartedSubscribeObj = this.responderListService.isAnyResponderAoaStarted.subscribe(anyAoaEnabled => this.aoa_enabled = anyAoaEnabled);
	}

	ngOnDestroy() {
		console.info("ngOnDestroy of cci-chart component");

		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.continuousDataIsConnectedSubscribeObj.unsubscribe();
		this.rtlsUtilServiceInitializeDoneSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
		this.responderListServiceOnResponderListChangeSubscribeObj.unsubscribe();
		this.responderListServiceIsAnyResponderCciStartedSubscribeObj.unsubscribe();
	}

	initChart() {
		let selectedDeviceList = [];
		let label_length = this.barChartLabels.length;

		if (this._deviceList !== undefined) { selectedDeviceList = this._deviceList.filter(d => d.selected); }


		if (selectedDeviceList.length > 0 && this.barChartLabels.length > 0) {

			this.barChartData = Array.from(
				selectedDeviceList,
				(d: SelectedDevice) => {
					return {
						data: Array<number>(label_length).fill(0),
						label: d.bd_address
					};
				}
			);

			this.barChartColors = Array.from(
				selectedDeviceList,
				(d: SelectedDevice) => {
					return { backgroundColor: d.graph_color };
				}
			);
		} else {
			this.barChartData = [{
				data: Array<number>(label_length).fill(0),
				label: ""
			}];
			this.barChartColors = [];
		}

		if (!this.auto_scale_enabled) {
			this.barChartOptions.scales.yAxes[0].ticks = {
				min: this.scale_min,
				max: this.scale_max,
				stepSize: this.scale_step
			};
		}
	}

	updateChart2(recv_data) {
		if ("connHandle" in recv_data.message.payload && recv_data.message.payload.connHandle !== this.active_responder.connection_handle) {
			return
		}

		let channel_index = this.barChartLabels.indexOf(
			recv_data.message.payload.channel.toString()
		);

		var chartData = this.barChartData.find(
			x => x.label === recv_data.identifier
		);

		if (channel_index !== -1 && chartData) {
			chartData.data[channel_index] = recv_data.message.payload.angle

			this.barChartData = this.barChartData.slice();
			this.rtlsUtilService.logMessages.next(recv_data);
		}
	}

	downloadCanvas(event) {
		var canvas = document.getElementById("cci-aoa-bar-canvas") as HTMLCanvasElement;

		var context = canvas.getContext("2d");

		//cache height and width
		var w = canvas.width;
		var h = canvas.height;

		var data = context.getImageData(0, 0, w, h);

		var compositeOperation = context.globalCompositeOperation;

		context.globalCompositeOperation = "destination-over";
		context.fillStyle = "#fff";
		context.fillRect(0, 0, w, h);

		var imageData = canvas.toDataURL("image/jpeg");

		context.clearRect(0, 0, w, h);
		context.putImageData(data, 0, 0);
		context.globalCompositeOperation = compositeOperation;

		var a = document.createElement("a");
		a.href = imageData;
		a.download = "cci.jpeg";
		a.click();
	}

	showAoaConfiguration() {
		const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
			width: "900px",
			height: "auto",
			disableClose: true,
			data: {
				configuration: this.configurationService.getCurrentConfiguration(),
				active_feature: SupportedFeatures.AOA,
				selected_tab: 4
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log("The dialog was closed", result);
			if (result !== undefined) {
				this.configurationService.setConfiguration(result, ConfigurationType.AOA);
			}
		});
	}

	auto_scale() {
		this.auto_scale_enabled = !this.auto_scale_enabled;

		this.update_scale();
	}

	update_scale() {
		if (this.chart) {
			if (this.auto_scale_enabled) {
				delete this.chart.chart.options.scales.yAxes[0].ticks;
			} else {
				this.chart.chart.options.scales.yAxes[0].ticks = {
					min: this.scale_min,
					max: this.scale_max,
					stepSize: this.scale_step
				};
			}

			this.chart.chart.update();
			this.chart.chart.render();
		}
	}

}
