import { Component, OnInit, Input, ViewChild, OnDestroy } from "@angular/core";
import { MatTableDataSource, MatSort, MatSortable } from "@angular/material";
import { Responder } from "app/interfaces/responder";
import { Configuration } from "app/interfaces/configuration";
import { ConfigurationService } from "app/services/configuration.service";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { SetupCapability } from "app/interfaces/setup-capability";
import { ResponderListService } from "app/services/responder-list.service";
import { ToastrService } from "ngx-toastr";
import "rxjs/add/observable/interval";
import "rxjs/add/operator/takeUntil";
import { Subject } from "rxjs";
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { environment } from 'environments/environment'


@Component({
	selector: "responder-list",
	templateUrl: "./responder-list.component.html",
	styleUrls: ["./responder-list.component.scss"],
	// encapsulation: ViewEncapsulation.None,
})
export class ResponderListComponent implements OnInit, OnDestroy {
	aoa_raw_only = environment.aoa_raw_only;

	@Input() showHeader: boolean = true;

	@Input() error_occur: boolean = false;

	@Input() selectedFeature: string;

	@ViewChild(MatSort, { static: true }) sort: MatSort;

	private stopHadmInterval$ = new Subject<void>();

	private _configuration: Configuration;

	private setup_capability: SetupCapability = {
		multi_connection_supported: false,
		cci_supported: false,
		aoa_supported: false,
		hadm_supported: false,
	};

	public supportedFeatures = SupportedFeatures;

	MAX_RESPONDER = 8;
	enable_scan = true;
	any_responder_connected = false;
	number_of_connected_responder = 0;
	any_cci_started = false;
	any_aoa_started = false;
	any_hadm_started = false;
	public in_sync_process = false;
	public is_any_responder_synced = false;
	public is_any_responder_padv_report_started = false;
	public is_any_responder_cl_aoa_started = false;

	dataSource = new MatTableDataSource<Responder>([]);

	private subscribeList = [];

	constructor(
		private toastr: ToastrService,
		private configurationService: ConfigurationService,
		private rtlsUtilService: RtlsUtilService,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		console.log("Responder List Init");

		this.subscribeList.push(this.configurationService.onConfigurationChange().subscribe((configuration) => {
			this._configuration = configuration;
			console.log("Configuration updated in responder component");
		}));
		this.subscribeList.push(this.responderListService.on_responder_list_change().subscribe((responderList) => {
			this.dataSource.data = responderList;

			this.enable_scan = true;
			let connected_responders_length = this.responderListService.get_connected_responders().length;
			if (connected_responders_length > 0) {
				this.enable_scan = connected_responders_length < (this.setup_capability.multi_connection_supported ? this.MAX_RESPONDER : 1);
			}
		}));
		this.subscribeList.push(this.responderListService.isAnyResponderConnected.subscribe(
			(status) => (this.any_responder_connected = status)
		));
		this.subscribeList.push(this.responderListService.isAnyResponderCciStarted.subscribe(
			(status) => (this.any_cci_started = status)
		));
		this.subscribeList.push(this.responderListService.isAnyResponderAoaStarted.subscribe(
			(status) => (this.any_aoa_started = status)
		));
		this.subscribeList.push(this.responderListService.isAnyResponderHadmStarted.subscribe(
			(status) => (this.any_hadm_started = status)
		));
		this.subscribeList.push(this.rtlsUtilService.setupCapability.subscribe(
			(setupCapability) => {
				this.setup_capability = setupCapability;
			}
		));
		this.subscribeList.push(this.rtlsUtilService.inSyncProcess.subscribe(in_process => {
			this.in_sync_process = in_process;
		}));
		this.subscribeList.push(this.responderListService.isAnyResponderSynced.subscribe(flag => {
			this.is_any_responder_synced = flag;
		}));
		this.subscribeList.push(this.responderListService.isAnyResponderPadvReportStarted.subscribe(flag => {
			this.is_any_responder_padv_report_started = flag;
		}));
		this.subscribeList.push(this.responderListService.isAnyResponderClAoaStarted.subscribe(flag => {
			this.is_any_responder_cl_aoa_started = flag;
		}));

		this.dataSource.sort = this.sort;
		this.dataSource.sortingDataAccessor = (data, attribute) => {
			switch (attribute) {
				case "rssi":
					return Math.abs(data[attribute]);
				default:
					return data[attribute];
			}
		};
		this.dataSource.sort.sort(<MatSortable>{
			id: "rssi",
			start: "asc",
		});
	}

	ngOnDestroy() {
		console.log("Responder List Destroy")

		for (let index = 0; index < this.subscribeList.length; index++) {
			const element = this.subscribeList[index];
			element.unsubscribe();
		}
	}

	sortChange(e) {
		if (e.direction !== "") {
			this.dataSource.sortData(
				this.dataSource.filteredData,
				this.dataSource.sort
			);
		}
	}

	scan_for_responder() {
		this.rtlsUtilService.scan(this._configuration.scan_connection.scan_time, this.selectedFeature);
	}

	async connection(row) {
		if ("is_connected" in row && row.is_connected) {
			let row_was_in_view = row.in_view;

			if (this.stopHadmInterval$) {
				this.stopHadmInterval$.next();
			}

			await this.rtlsUtilService.disconnect(row);

			if (row_was_in_view && this.any_responder_connected) {
				let connected_responders = this.responderListService.get_connected_responders();
				this.responderListService.set_responder_in_view(connected_responders[0]);
			}
		} else {
			let current_connected_responders_num = this.responderListService.get_connected_responders().length;

			if (current_connected_responders_num === 1 && !this.setup_capability.multi_connection_supported) {
				this.toastr.info("Current Device not Supporting Multi Connection", "Multi Connection Not Supported", {
					timeOut: 3000,
					positionClass: "toast-bottom-full-width",
				});

				return;
			}

			// Retrun with new HADM
			// if ( (current_connected_responders_num + 1) > 1 && this.any_tof_started) {
			// 	this.disable_all_tof();
			// }

			await this.rtlsUtilService.connect(row, this._configuration.scan_connection.connection_interval);

			if (this.responderListService.get_connected_responders().length == 1) {
				this.responderListService.set_responder_in_view(row);
			}
		}

		let connected_responders_length = this.responderListService.get_connected_responders().length;
		this.number_of_connected_responder = connected_responders_length;
		this.enable_scan = connected_responders_length < (this.setup_capability.multi_connection_supported ? this.MAX_RESPONDER : 1);
	}

	disconnect_all_responder() {
		this.rtlsUtilService.diconnect_all_responders();

		let connected_responders_length = this.responderListService.get_connected_responders().length;
		this.number_of_connected_responder = connected_responders_length;
		this.enable_scan = connected_responders_length < (this.setup_capability.multi_connection_supported ? this.MAX_RESPONDER : 1);
	}

	cci(row) {
		if ("is_connected" in row && row.is_connected) {
			if ("cci_started" in row && row.cci_started) {
				this.rtlsUtilService.disable_cci(row);
			} else {
				this.rtlsUtilService.enable_cci(row);
			}
		}
	}

	disable_all_cci() {
		this.rtlsUtilService.disable_all_cci();
	}

	aoa(row) {
		if ("is_connected" in row && row.is_connected) {
			if ("aoa_started" in row && row.aoa_started) {
				this.rtlsUtilService.disable_aoa(row);
			} else {
				this.rtlsUtilService.enable_aoa(
					row,
					this._configuration.aoa_parameters
				);
			}
		}
	}

	disable_all_aoa() {
		this.rtlsUtilService.disable_all_aoa();
	}

	update_view_selection(e, row) {
		e.preventDefault();

		if (row.is_connected) {
			this.responderListService.set_responder_in_view(row);
		}
	}

	getDisplayedColumns(): string[] {
		let displayedColumnsHADM = [
			"select",
			"bd_address",
			"rtls_role",
			"rssi",
			"aConnect",
			"aCCI",
			"aHADM",
		];
		let displayedColumnsAOA = [
			"select",
			"bd_address",
			"rtls_role",
			"rssi",
			"aConnectionHandle",
			"aConnect",
			"aCCI",
			"aAoA",
		];
		let displayedColumnsAOAMR = [
			"select",
			"bd_address",
			"rssi",
			"conn_role",
			"aConnectionHandle",
			"aConnect",
			"aCCI",
			"aAoA",
		];
		let displayedColumnsCLAOA = [
			"aAddToPerAdvList",
			// "select",
			"bd_address",
			// "rtls_role",
			"rssi",
			"aConnectionHandle",
			"aPeriodicInterval",
			"aSync",
			"aPerdiocAdvertiserReport",
			"aClAoA",
			"aSyncColor"
		];

		if (this.aoa_raw_only) {
			displayedColumnsAOA = displayedColumnsAOA.filter(i => i !== 'aAoA');
			displayedColumnsAOAMR = displayedColumnsAOAMR.filter(i => i !== 'aAoA');
			displayedColumnsCLAOA = displayedColumnsCLAOA.filter(i => i !== 'aClAoA');
		}

		let displayed_columns = null;

		switch (this.selectedFeature) {
			case SupportedFeatures.AOA:
				displayed_columns = displayedColumnsAOA;
				break;
			case SupportedFeatures.AOA_MR:
				displayed_columns = displayedColumnsAOAMR;
				break;
			case SupportedFeatures.CL_AOA:
				displayed_columns = displayedColumnsCLAOA;
				break;
			default:
				displayed_columns = displayedColumnsAOA;
				break;
		}

		return displayed_columns
	}

	async sync(row) {
		if ("is_synced" in row && row.is_synced) {
			await this.rtlsUtilService.term_sync(row);
		} else {
			this._configuration.cl_aoa_parameters.cl_aoa_scanner.use_periodic_adv_list = false;
			await this.rtlsUtilService.sync(
				row,
				this._configuration.cl_aoa_parameters.cl_aoa_scanner,
				this._configuration.scan_connection.scan_time
			);
		}
	}

	cancel_sync() {
		this.rtlsUtilService.cancel_sync();
	}

	add_to_per_adv_list(row) {
		this.rtlsUtilService.add_responder_into_periodic_adv_list(row);
	}

	perdioc_adv_report(row) {
		if (!('padv_report_started' in row) || row.padv_report_started === false) {
			this.rtlsUtilService.enable_periodic_adv_report(row);
		} else {
			this.rtlsUtilService.disable_periodic_adv_report(row);
		}
	}

	cl_aoa(row) {
		if ("is_synced" in row && row.is_synced) {
			if ("cl_aoa_started" in row && row.cl_aoa_started) {
				this.rtlsUtilService.disable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			} else {
				this.rtlsUtilService.enable_cl_aoa(
					row,
					this._configuration.cl_aoa_parameters.cl_aoa_config
				);
			}
		}
	}

	async term_all_sync() {
		let synced_responders = this.responderListService.get_synced_responders();
		for (const responder of synced_responders) {
			await this.rtlsUtilService.term_sync(responder);
		}
	}

	async disable_all_reports() {
		let started_reports = this.responderListService.get_padv_report_started_responders();
		for (const responder of started_reports) {
			await this.rtlsUtilService.disable_periodic_adv_report(responder);
		}
	}

	async disable_all_cl_aoa() {
		let started_cl_aoa = this.responderListService.get_cl_aoa_started_responders();
		for (const responder of started_cl_aoa) {
			await this.rtlsUtilService.disable_cl_aoa(responder, this._configuration.cl_aoa_parameters.cl_aoa_config);
		}
	}
}
