import { Component, OnInit, HostListener } from '@angular/core';
import { RestApiService } from './services/rest-api.service';
import { ToastrService } from 'ngx-toastr';
import { MatDialog } from '@angular/material';
import { ForceRefreshDialogComponent } from './dialogs/force-refresh-dialog/force-refresh-dialog.component';


@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
	title = 'RTLS UI';
	version = '1.0.0'

	private ctrl_f5: boolean = false;

	@HostListener('window:unload', ['$event'])
	unloadHandler(event) {
		this.restApiService.shutdown().subscribe(() => { });
	}

	@HostListener('window:window:beforeunload', ['$event'])
	onBeforeUnload(event) {
		return false;
	}

	@HostListener('window:keydown', ['$event'])
	hotkeys(event) {
		// console.log(event);

		if (navigator.platform.indexOf('Mac') !== -1) {
			this.ctrl_f5 = event.metaKey && event.shiftKey && event.keyCode === 82;
		} else {
			this.ctrl_f5 = event.ctrlKey && (event.keyCode === 116 || event.keyCode === 82);
		}

		// console.log(this.ctrl_f5);

		if (this.ctrl_f5) {
			localStorage.setItem('force_reset', String(this.ctrl_f5));
			localStorage.setItem("last_version", this.version);
		}
	}

	constructor(
		private restApiService: RestApiService,
		public dialog: MatDialog
	) { }

	ngOnInit() {
		this.ctrl_f5 = false;

		this.restApiService.getAppVersion().subscribe(data => {
			this.version = data.version;
			let last_loaded_version = localStorage.getItem("last_version");

			if (last_loaded_version === null || this.version !== last_loaded_version) {
				const dialogRef = this.dialog.open(ForceRefreshDialogComponent, {
					width: '700px',
					height: 'auto',
					disableClose: true
				});
			}
		});
	}


}
