import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Device } from 'app/interfaces/device';

@Component({
	selector: 'device-box',
	templateUrl: './device-box.component.html',
	styleUrls: ['./device-box.component.scss']
})
export class DeviceBoxComponent implements OnInit {

	@Input() device: Device;

	@Output() deviceSelected = new EventEmitter<Device>();

	constructor() { }

	ngOnInit() {
	}

	boxClick(device) {
		device.selected = !device.selected

		this.deviceSelected.emit(device);
	}

}
