import { Component, OnInit, Input, OnDestroy, ViewChild } from "@angular/core";
import { ContinuousDataService } from "app/services/continuous-data.service";
import { SelectedDevice } from "app/interfaces/selected-device";
import { ChartDataSets, ChartOptions } from "chart.js";
import { Label, Color, BaseChartDirective } from "ng2-charts";
import { RtlsUtilService } from "app/services/rtls-util.service";
import { ConfigurationService } from "app/services/configuration.service";
import { MatDialog } from "@angular/material";
import { ConfigurationDialogComponent } from "app/dialogs/configuration-dialog/configuration-dialog.component";
import { SampleCounter } from "app/interfaces/sample-counter";
import { ResponderListService } from "app/services/responder-list.service";
import { Responder } from "app/interfaces/responder";
import { ConfigurationType } from 'app/enums/configuration-type.enum';
import { SupportedFeatures } from 'app/enums/supported-features.enum';

@Component({
	selector: "aoa-chart",
	templateUrl: "./aoa-chart.component.html",
	styleUrls: ["./aoa-chart.component.scss"]
})
export class AoaChartComponent implements OnInit, OnDestroy {
	_deviceList: SelectedDevice[];

	@ViewChild(BaseChartDirective, { static: false })
	public chart: BaseChartDirective;

	public lineChartData: ChartDataSets[];
	public lineChartLabels: Label[];
	public lineChartOptions: ChartOptions = {
		responsive: true,
		maintainAspectRatio: false,

		scales: {
			xAxes: [
				{
					display: false,
					scaleLabel: {
						display: true,
						labelString: "Sample",
						fontColor: "red"
					}
				}
			],
			yAxes: [
				{
					// ticks: {
					// 	min: -180,
					// 	max: 180,
					// 	stepSize: 20
					// },
					scaleLabel: {
						display: true,
						labelString: "Angle",
						fontColor: "red"
					}
				}
			]
		},
		elements: {
			line: {
				fill: false
			}
		},
		tooltips: {
			enabled: false
		},
		plugins: {
			datalabels: {
				labels: {
					title: null
				}
			}
		}
	};
	public lineChartColors: Color[] = [];
	public lineChartLegend = false;
	public lineChartType = "line";

	private display_channels: string[] = [];
	private sample_counter: SampleCounter[];

	private number_of_points_to_display: number;
	private average_each_n_point: number;
	private scale_min: number;
	private scale_max: number;
	private scale_step: number;

	private configurationServiceOnConfigurationChangeSubscribeObj: any;
	private continuousDataIsConnectedSubscribeObj: any;
	private rtlsUtilServiceInitializeDoneSubscribeObj: any;
	private rtlsUtilServiceDeviceListSubscribeObj: any;
	private responderListServiceOnResponderListChangeSubscribeObj: any;
	private responderListServiceIsAnyResponderAoaStartedSubscribeObj: any;

	private aoa_enabled = false;
	private auto_scale_enabled = false;
	private active_responder: Responder;

	constructor(
		private configurationService: ConfigurationService,
		private continuousData: ContinuousDataService,
		private rtlsUtilService: RtlsUtilService,
		public dialog: MatDialog,
		private responderListService: ResponderListService
	) { }

	ngOnInit() {
		console.info("ngOnInit of aoa-chart component");

		this.configurationServiceOnConfigurationChangeSubscribeObj = this.configurationService
			.onConfigurationChange()
			.subscribe(new_configuration => {
				this.number_of_points_to_display =
					new_configuration.general.aoa_graph_parameters.number_of_points_to_display;
				this.average_each_n_point =
					new_configuration.general.aoa_graph_parameters
						.average_each_n_point - 1;

				this.scale_min =
					new_configuration.general.aoa_graph_parameters.scale.min;
				this.scale_max =
					new_configuration.general.aoa_graph_parameters.scale.max;
				this.scale_step =
					new_configuration.general.aoa_graph_parameters.scale.step;

				this.display_channels = new_configuration.aoa_parameters.aoa_display_channels;

				this.update_scale();
			});

		this.continuousDataIsConnectedSubscribeObj = this.continuousData
			.isConnected()
			.subscribe(event => {
				if (event == "connected") {
					console.log("AoA chart subscribe to flow data");
					this.continuousData.data.subscribe(
						recv_data => {
							if (
								recv_data &&
								recv_data.message.command ===
								"RTLS_CMD_AOA_RESULT_ANGLE"
							) {
								setTimeout(() => {
									this.updateChart2(recv_data);
								}, 10);
							}
						},
						error => {
							console.log(error);
						}
					);
				}
			});

		this.rtlsUtilServiceInitializeDoneSubscribeObj = this.rtlsUtilService.initialize_done.subscribe(
			() => {
				this.initChart();
			}
		);

		this.rtlsUtilServiceDeviceListSubscribeObj = this.rtlsUtilService.deviceList.subscribe(
			deviceList => {
				this._deviceList = deviceList;

				this.initChart();
			}
		);

		this.responderListServiceOnResponderListChangeSubscribeObj = this.responderListService.on_responder_list_change().subscribe(responderList => {
			// console.log(responderList);
			if (this.responderListService.is_any_responder_in_view()) {
				let responders_in_view = responderList.filter(s => s.in_view);
				if (responders_in_view[0] !== this.active_responder) {
					this.initChart();
				}
				this.active_responder = responders_in_view[0];
			}
		});

		this.responderListServiceIsAnyResponderAoaStartedSubscribeObj = this.responderListService.isAnyResponderAoaStarted.subscribe(anyAoaEnabled => this.aoa_enabled = anyAoaEnabled);
	}

	ngOnDestroy() {
		console.info("ngOnDestroy of aoa-chart component");

		this.configurationServiceOnConfigurationChangeSubscribeObj.unsubscribe();
		this.continuousDataIsConnectedSubscribeObj.unsubscribe();
		this.rtlsUtilServiceInitializeDoneSubscribeObj.unsubscribe();
		this.rtlsUtilServiceDeviceListSubscribeObj.unsubscribe();
		this.responderListServiceOnResponderListChangeSubscribeObj.unsubscribe();
		this.responderListServiceIsAnyResponderAoaStartedSubscribeObj.unsubscribe();
	}

	initChart() {
		let selectedDeviceList = [];

		if (this._deviceList !== undefined) { selectedDeviceList = this._deviceList.filter(d => d.selected); }

		this.lineChartLabels = Array<string>(this.number_of_points_to_display)
			.fill("")
			.map((v, i) => i.toString());

		if (selectedDeviceList.length > 0) {

			this.lineChartData = Array.from(
				selectedDeviceList,
				(d: SelectedDevice) => {
					return {
						data: [],
						label: d.bd_address
					};
				}
			);

			this.lineChartColors = Array.from(
				selectedDeviceList,
				(d: SelectedDevice) => {
					return { borderColor: d.graph_color };
				}
			);

			this.sample_counter = Array.from(
				selectedDeviceList,
				(d: SelectedDevice) => {
					return {
						counter: 0,
						sample_sum: 0
					};
				}
			);
		} else {
			this.lineChartData = [{
				data: [],
				label: ""
			}];
			this.lineChartColors = [];
		}

		if (!this.auto_scale_enabled) {
			this.lineChartOptions.scales.yAxes[0].ticks = {
				min: this.scale_min,
				max: this.scale_max,
				stepSize: this.scale_step
			};
		}
	}

	updateChart2(recv_data) {
		if ("connHandle" in recv_data.message.payload && recv_data.message.payload.connHandle !== this.active_responder.connection_handle) {
			return;
		}

		// Exit if current channels is not in display channels array
		if (this.display_channels.indexOf(recv_data.message.payload.channel.toString()) === -1) {
			return;
		}

		let chatDataIndex = this.lineChartData.findIndex(
			x => x.label === recv_data.identifier
		);

		var chartData = this.lineChartData[chatDataIndex];
		let angle = recv_data.message.payload.angle;

		if (this.sample_counter[chatDataIndex].counter === this.average_each_n_point) {
			if (chartData.data.length + 1 >= this.lineChartLabels.length) {
				chartData.data.shift();
			}

			let avg_angle =
				this.sample_counter[chatDataIndex].sample_sum /
				this.sample_counter[chatDataIndex].counter;
			avg_angle = this.average_each_n_point === 0 ? angle : avg_angle;

			chartData.data.push(avg_angle);

			recv_data.message.payload.angle = avg_angle;
			// this.rtlsUtilService.logMessages.next(recv_data);

			this.lineChartData = this.lineChartData.slice();

			this.sample_counter[chatDataIndex].counter = 0;
			this.sample_counter[chatDataIndex].sample_sum = 0;

		} else {
			this.sample_counter[chatDataIndex].counter += 1;
			this.sample_counter[chatDataIndex].sample_sum += angle;
		}
	}

	downloadCanvas(event) {
		var canvas = document.getElementById("aoa-canvas") as HTMLCanvasElement;

		var context = canvas.getContext("2d");

		//cache height and width
		var w = canvas.width;
		var h = canvas.height;

		var data = context.getImageData(0, 0, w, h);

		var compositeOperation = context.globalCompositeOperation;

		context.globalCompositeOperation = "destination-over";
		context.fillStyle = "#fff";
		context.fillRect(0, 0, w, h);

		var imageData = canvas.toDataURL("image/jpeg");

		context.clearRect(0, 0, w, h);
		context.putImageData(data, 0, 0);
		context.globalCompositeOperation = compositeOperation;

		var a = document.createElement("a");
		a.href = imageData;
		a.download = "aoa.jpeg";
		a.click();

		console.log(this.lineChartOptions.scales.yAxes[0].ticks);
		delete this.lineChartOptions.scales.yAxes[0].ticks;
		console.log(this.lineChartOptions.scales.yAxes[0].ticks);

		this.lineChartData = this.lineChartData.slice();
	}

	showAoaConfiguration() {
		const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
			width: "900px",
			height: "auto",
			disableClose: true,
			data: {
				configuration: this.configurationService.getCurrentConfiguration(),
				active_feature: SupportedFeatures.AOA,
				selected_tab: 4
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log("The dialog was closed", result);
			if (result !== undefined) {
				this.configurationService.setConfiguration(result, ConfigurationType.AOA);
			}
		});
	}

	auto_scale() {
		this.auto_scale_enabled = !this.auto_scale_enabled;

		this.update_scale();
	}

	update_scale() {
		if (this.chart) {
			if (this.auto_scale_enabled) {
				delete this.chart.chart.options.scales.yAxes[0].ticks;
			} else {
				this.chart.chart.options.scales.yAxes[0].ticks = {
					min: this.scale_min,
					max: this.scale_max,
					stepSize: this.scale_step
				};
			}

			this.chart.chart.update();
			this.chart.chart.render();
		}
	}
}
