import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Device } from "app/interfaces/device";
import { Observable } from "rxjs";
import { SelectedDevice } from "app/interfaces/selected-device";
import { Responder } from "app/interfaces/responder";
import { AoaParameters, Configuration, ClAoaScannerParameters, ClAoaConfigParameters } from "app/interfaces/configuration";
import { SetupCapability } from "app/interfaces/setup-capability";
import { environment } from "../../environments/environment";
import { ConnectionStatus } from "app/interfaces/connection-status";
import { ActiveResponder } from "app/interfaces/active-responder";
import { Coordinator } from "app/interfaces/coordinator";

@Injectable({
	providedIn: "root"
})
export class RestApiService {
	private env = environment;

	private SERVER_IP: string = this.env.server_ip;
	private SERVER_PORT: number = this.env.server_port;

	constructor(private http: HttpClient) { }

	getAppVersion(): Observable<any> {
		return this.http.get<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/appVersion`
		);
	}

	getWarningMessages(): Observable<any> {
		return this.http.get<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/warnings`);
	}

	getConfiguration(version: number): Observable<Configuration> {
		return this.http.put<Configuration>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`, JSON.stringify({ version: version }));
	}

	setConfiguration(v: Configuration): Observable<any> {
		return this.http.post<Configuration>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/configuration`,
			JSON.stringify(v)
		);
	}

	getDeviceList(feature): Observable<Device[]> {
		return this.http.get<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices-${feature}`
		);
	}

	indentifyDevices(deviceList: Device[], feature): Observable<Device[]> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/devices-${feature}`,
			JSON.stringify(deviceList)
		);
	}

	initializeRtlsUtil(
		deviceList: Device[],
		websocket_port: number,
		rtlsutil_timeout: number,
		aoa_raw_only: boolean
	): Observable<SelectedDevice[]> {
		return this.http.post<SelectedDevice[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`,
			JSON.stringify({
				deviceList: deviceList,
				websocketPort: websocket_port,
				rtlsUtilTimeout: rtlsutil_timeout,
				aoa_raw_only: aoa_raw_only
			})
		);
	}

	finalizeRtlsUtil(): Observable<any> {
		return this.http.delete<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`
		);
	}

	getSetupCapability(): Observable<SetupCapability> {
		return this.http.get<SetupCapability>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/initialize`
		);
	}

	scan(scan_time: Number): Observable<Responder[]> {
		return this.http.post<Responder[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/scan`,
			JSON.stringify({
				scan_time: scan_time
			})
		);
	}

	responder_scan(scan_time: Number, responder: ActiveResponder): Observable<Coordinator[]> {
		return this.http.post<Coordinator[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/responder/scan`,
			JSON.stringify({
				scan_time: scan_time,
				responder: responder
			})
		);
	}

	connect(responder: Responder, connection_interval: Number): Observable<ConnectionStatus> {
		return this.http.post<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connection`,
			JSON.stringify({
				responder: responder,
				connection_interval: connection_interval
			})
		);
	}

	responder_connect(coordinator: Coordinator, connection_interval: Number, responder: ActiveResponder): Observable<ConnectionStatus> {
		return this.http.post<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/responder/connection`,
			JSON.stringify({
				coordinator: coordinator,
				connection_interval: connection_interval,
				responder: responder
			})
		);
	}

	disconnect(responder: Responder): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/connection`,
			JSON.stringify(responder)
		);
	}

	enable_cci(responder: Responder): Observable<any> {
		return this.http.post<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`,
			JSON.stringify(responder)
		);
	}

	disable_cci(responder: Responder): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cci`,
			JSON.stringify(responder)
		);
	}

	enable_aoa(responder: Responder, aoa_parameters: AoaParameters): Observable<any> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`,
			JSON.stringify({ responder: responder, aoa_parameters: aoa_parameters })
		);
	}

	disable_aoa(responder: Responder): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/aoa`,
			JSON.stringify(responder)
		);
	}

	shutdown(): Observable<any> {
		return this.http.get(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/shutdown`
		);
	}

	sync(responder: Responder, scanner_options: ClAoaScannerParameters, scan_time: number): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`, JSON.stringify({
			responder: responder,
			options: scanner_options,
			scan_time: scan_time
		}));
	}

	cancel_sync() {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`);
	}

	term_sync(responder: Responder): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/sync`, JSON.stringify(responder));
	}

	add_responder_into_periodic_adv_list(responder: Responder): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`, JSON.stringify({ responder: responder }));
	}

	remove_responder_from_periodic_adv_list(responder: Responder): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`, JSON.stringify({ responder: responder }));
	}

	clear_periodic_adv_list(): Observable<any> {
		return this.http.delete<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv-list`);
	}

	enable_periodic_adv_report(responder: Responder): Observable<any> {
		return this.http.post<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv`, JSON.stringify({ responder: responder }));
	}

	disable_periodic_adv_report(responder: Responder): Observable<any> {
		return this.http.put<any>(`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa/per-adv`, JSON.stringify({ responder: responder }));
	}

	enable_cl_aoa(responder: Responder, cl_aoa_config_parameters: ClAoaConfigParameters): Observable<any> {
		return this.http.post<Device[]>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa`,
			JSON.stringify({ responder: responder, cl_aoa_parameters: cl_aoa_config_parameters })
		);
	}

	disable_cl_aoa(responder: Responder, cl_aoa_config_parameters: ClAoaConfigParameters): Observable<any> {
		return this.http.put<any>(
			`http://${this.SERVER_IP}:${this.SERVER_PORT}/rtls-ui/api/cl-aoa`,
			JSON.stringify({ responder: responder, cl_aoa_parameters: cl_aoa_config_parameters })
		);
	}

}
