import { Component, OnInit } from '@angular/core';
import { ChartOptions, ChartDataSets, ChartType, ChartPoint } from 'chart.js';
import { Label, Color } from 'ng2-charts';
import { ContinuousDataService } from 'app/services/continuous-data.service';
import { RtlsUtilService } from 'app/services/rtls-util.service';
import { ResponderListService } from 'app/services/responder-list.service';
import { MatDialog } from '@angular/material';
import { ConfigurationDialogComponent } from 'app/dialogs/configuration-dialog/configuration-dialog.component';
import { ConfigurationService } from 'app/services/configuration.service';
import { ConfigurationType } from 'app/enums/configuration-type.enum';
import { SupportedFeatures } from 'app/enums/supported-features.enum';
import { Responder } from 'app/interfaces/responder';


@Component({
	selector: 'responder-by-angle-chart',
	templateUrl: './responder-by-angle-chart.component.html',
	styleUrls: ['./responder-by-angle-chart.component.scss']
})
export class ResponderByAngleChartComponent implements OnInit {

	private synced_responders: Responder[] = [];

	private radius: number = 8;
	private minScale: number = -1 * (this.radius + 2);
	private maxScale: number = this.radius + 2;

	public scatterChartOptions: ChartOptions = {
		responsive: true,
		maintainAspectRatio: false,

		scales: {
			xAxes: [{
				ticks: {
					min: this.minScale,
					max: this.maxScale,
					stepSize: 1,
					display: false
				}
			}],
			yAxes: [{
				ticks: {
					min: this.minScale,
					max: this.maxScale,
					stepSize: 1,
					display: false
				}
			}]
		},
		legend: {
			display: false,
		},
		tooltips: {
			enabled: true,
			callbacks: {
				label: (item, data) => {
					return `${(data.datasets[0].data[item.index] as ChartPoint).r} °`
				}
			}
		},
		plugins: {
			datalabels: {
				// labels: {

				// 	// 	title: null,
				// },
				formatter: (value, context) => {
					let point_name = "Coordinator"
					if (this.synced_responders[context.dataIndex - 1] !== undefined) {
						point_name = this.synced_responders[context.dataIndex - 1].bd_address.slice(9);
					}

					return `${point_name} [ ${value.r} ° ]`;
				},
				textAlign: 'center',
				anchor: 'end',
				align: 'end',
				borderColor: 'red',
				borderWidth: 1,
				font: {
					size: 12,
					weight: 'bold'
				}
			},
		},
	};
	public scatterChartLabels: Label[] = [];

	public scatterChartData: ChartDataSets[] = [
		{
			data: [],
			label: 'Location',
			pointRadius: 10,
		}
	];
	public scatterChartColors: Color[] = [];
	public scatterChartType: ChartType = 'scatter';

	private s1: any;
	private s2: any;
	private s3: any;
	private s4: any;
	private s5: any;
	private s6: any;

	public is_any_responder_cl_aoa_started: boolean = false;
	private channels_to_display: string[] = [];

	constructor(
		private continuousData: ContinuousDataService,
		private rtlsUtilService: RtlsUtilService,
		private responderListService: ResponderListService,
		private configurationService: ConfigurationService,
		public dialog: MatDialog
	) { }

	ngOnInit() {
		console.debug("ngOnInit of ResponderByAngleChartComponent");

		this.s1 = this.continuousData.isConnected().subscribe(event => {
			if (event == "connected") {
				console.log("ResponderByAngleChartComponent subscribe to flow data");
				this.continuousData.data.subscribe(
					recv_data => {
						if (recv_data && recv_data.message.command === "RTLS_CMD_CL_AOA_RESULT_ANGLE") {
							setTimeout(() => {
								this.updateChart(recv_data);
							}, 15);
						}
					},
					error => {
						console.log(error);
					}
				);
			}
		});

		this.s2 = this.rtlsUtilService.initialize_done.subscribe(() => {
			this.initChart();
		});

		this.s3 = this.responderListService.on_responder_list_change().subscribe(responder_list => {
			this.initChart();
		});

		this.s4 = this.responderListService.on_periodic_adv_responder_list_change().subscribe(periodic_adv_responder_list => {
			this.initChart();
		});

		this.s5 = this.responderListService.isAnyResponderClAoaStarted.subscribe(status => {
			this.is_any_responder_cl_aoa_started = status;
		});

		this.s6 = this.configurationService.onConfigurationChange().subscribe(new_configuration => {
			this.channels_to_display = new_configuration.cl_aoa_parameters.cl_aoa_responder_by_angle_chart_params.channels;
			this.scatterChartColors = [{ backgroundColor: new_configuration.general.color_schema }];
		});
	}

	ngOnDestroy() {
		console.debug("ngOnDestroy of ResponderByAngleChartComponent");

		this.s1.unsubscribe();
		this.s2.unsubscribe();
		this.s3.unsubscribe();
		this.s4.unsubscribe();
		this.s5.unsubscribe();
		this.s6.unsubscribe();
	}

	initChart() {
		this.synced_responders = this.responderListService.get_synced_responders();
		this.synced_responders.sort((s1, s2) => s1.connection_handle - s2.connection_handle);

		this.scatterChartData = [
			{
				data: Array<ChartPoint>(this.synced_responders.length + 1).fill({ x: 0, y: 0, r: 0 }).map((v, i) => { return { x: i, y: i, r: 0 } }),
				label: 'Location',
				pointRadius: 10,
			}
		];
	}

	updateChart(recv_data) {
		if (this.channels_to_display.indexOf(recv_data.message.payload.channel.toString()) === -1) {
			return
		}

		let sync_handle = recv_data.message.payload.syncHandle;
		let responder_index = -2;

		for (let index = 0; index < this.synced_responders.length; index++) {
			const s = this.synced_responders[index];
			if (s.connection_handle === sync_handle) {
				responder_index = index;
				break;
			}
		}

		let angle = recv_data.message.payload.angle;
		let angle_rad = angle * (Math.PI / 180);

		let calced_x = this.radius * Math.cos(angle_rad);
		let calced_y = this.radius * Math.sin(angle_rad);

		this.scatterChartData[0].data[responder_index + 1] = {
			x: calced_x,
			y: calced_y,
			r: parseFloat(angle)
		}

		this.scatterChartData = this.scatterChartData.slice();
		this.rtlsUtilService.logMessages.next(recv_data);
	}

	showClAoaConfiguration() {
		const dialogRef = this.dialog.open(ConfigurationDialogComponent, {
			width: "900px",
			height: "auto",
			disableClose: true,
			data: {
				configuration: this.configurationService.getCurrentConfiguration(),
				active_feature: SupportedFeatures.CL_AOA,
				selected_tab: 5
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log("The dialog was closed", result);
			if (result !== undefined) {
				this.configurationService.setConfiguration(result, ConfigurationType.CL_AOA);
			}
		});
	}

	downloadCanvas(event) {
		var canvas = document.getElementById("scatter-canvas") as HTMLCanvasElement;

		var context = canvas.getContext("2d");

		//cache height and width
		var w = canvas.width;
		var h = canvas.height;

		var data = context.getImageData(0, 0, w, h);

		var compositeOperation = context.globalCompositeOperation;

		context.globalCompositeOperation = "destination-over";
		context.fillStyle = "#fff";
		context.fillRect(0, 0, w, h);

		var imageData = canvas.toDataURL("image/jpeg");

		context.clearRect(0, 0, w, h);
		context.putImageData(data, 0, 0);
		context.globalCompositeOperation = compositeOperation;

		var a = document.createElement("a");
		a.href = imageData;
		a.download = "responders-by-angle.jpeg";
		a.click();
	}

}
